<template>
  <div class="fx-message" :class="[type,{close:isClose,center}]" :style="messageStyle">
    <div class="content" v-if="!isHTML">{{ message }}</div>
    <div class="content" v-else v-html="message"></div>
    <span v-if="showClose" @click="closeClick"><i class="iconfont icon-multiply fs10"/></span>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, PropType, ref, SetupContext } from 'vue'
import { ComponentTypes, componentTypesArr } from '@/base/types'
import mitt, { Emitter } from 'mitt'

export interface MessageProps {
  message: string;
  type: ComponentTypes;
  center: boolean;
  offset: number;
  showClose: boolean;
  isHTML: boolean;
}

export default defineComponent({
  name: 'FMessage',
  props: {
    // 消息
    message: {
      type: String as PropType<string>,
      required: true
    },
    // 类型
    type: {
      type: String as PropType<ComponentTypes>,
      default: 'success',
      validator (val: string): boolean {
        return componentTypesArr.includes(val)
      }
    },
    // 文字居中
    center: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 距离顶部距离
    offset: {
      type: Number as PropType<number>,
      default: 20
    },
    // 可关闭
    showClose: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 是否是html
    isHTML: {
      type: Boolean as PropType<boolean>,
      default: false
    }
  },
  setup (props: MessageProps, ctx: SetupContext) {
    const currentIndex = ref<number>(0)
    const isClose = ref<boolean>(false)
    const messageMitt: Emitter = mitt()

    const closeMessage = (): void => {
      isClose.value = true
    }

    const setCurrentIndex = (val: number): void => {
      currentIndex.value = val
    }

    const messageStyle = computed(() => {
      return {
        top: `${currentIndex.value * 50 + props.offset}px`
      }
    })

    const closeClick = (): void => {
      messageMitt.emit('close', ctx.attrs.id)
    }

    return {
      isClose,
      currentIndex,
      messageStyle,
      closeClick,
      closeMessage,
      setCurrentIndex,
      messageMitt
    }
  }
})
</script>

<style scoped lang="scss">
@import "../../scss/base";

@keyframes message-enter {
  from {
    transform: translateX(-50%) translateY(-100%);
    opacity: 0;
  }
  to {
    transform: translateX(-50%) translateY(0);
    opacity: 1;
  }
}

@keyframes message-leave {
  from {
    transform: translateX(-50%) translateY(0);
    opacity: 1;
  }
  to {
    transform: translateX(-50%) translateY(-100%);
    opacity: 0;
  }
}

.fx-message {
  max-width: 80%;
  min-width: 300px;
  position: fixed;
  z-index: 1002;
  left: 50%;
  transform: translateX(-50%);
  height: $height-large;
  line-height: $height-large;
  padding: 0 30px;
  border-radius: $border-radius-default;
  color: #fff;
  transition: top 0.3s;
  animation: message-enter 0.3s forwards;
  @extend .text_hide;

  &.center {
    text-align: center;
  }

  &.close {
    animation: message-leave 0.3s forwards;
  }

  &.primary {
    background-color: $color-primary;
  }

  &.success {
    background-color: $color-success;
  }

  &.warning {
    background-color: $color-warning;
  }

  &.error {
    background-color: $color-error;
  }

  > span {
    cursor: pointer;
    display: inline-block;
    width: 10px;
    height: 10px;
    line-height: 10px;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
  }
}
</style>
