<template>
  <div class="fx-tag" :class="[size,type,theme]">
    <div class="content" :class="{mr10:closable}">
      <slot></slot>
    </div>
    <div class="close" v-if="closable" @click="closeClick">
      <i class="iconfont icon-multiply fs8"/>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType, SetupContext } from 'vue'
import {
  ComponentSize,
  componentSizeArr,
  ComponentTheme,
  componentThemeArr,
  ComponentTypes,
  componentTypesArr
} from '@/base/types'

interface TagProps {
  size: ComponentSize,
  type: ComponentTypes,
  closable: boolean,
  theme: ComponentTheme
}

export default defineComponent({
  name: 'FTag',
  props: {
    // 大小
    size: {
      type: String as PropType<ComponentSize>,
      default: 'default',
      validator (val: string): boolean {
        return componentSizeArr.includes(val)
      }
    },
    // 类型
    type: {
      type: String as PropType<ComponentTypes>,
      default: 'primary',
      validator (val: string): boolean {
        return componentTypesArr.includes(val)
      }
    },
    // 类型
    theme: {
      type: String as PropType<ComponentTheme>,
      default: 'plain',
      validator (val: string): boolean {
        return componentThemeArr.includes(val)
      }
    },
    // 是否显示关闭
    closable: {
      type: Boolean as PropType<boolean>,
      default: false
    }
  },
  emits: ['close'],
  setup (props: TagProps, ctx: SetupContext) {
    const closeClick = (): void => {
      ctx.emit('close')
    }

    return {
      closeClick
    }
  }
})
</script>

<style scoped lang="scss">
@import "../scss/base";

.fx-tag {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 0 10px;
  box-sizing: border-box;
  border: 1px solid transparent;
  border-radius: $border-radius-default;
  margin-right: 10px;

  &.light {
    background-color: #fff !important;
  }

  &.large {
    height: $height-large;
    line-height: calc(#{$height-large} - 2px);
  }

  &.middle {
    height: $height-middle;
    line-height: calc(#{$height-middle} - 2px);
  }

  &.default {
    height: $height-default;
    line-height: calc(#{$height-default} - 2px);
  }

  &.mini {
    height: $height-mini;
    line-height: calc(#{$height-mini} - 2px);
  }

  &.primary {
    border-color: $color-primary;
    background-color: $color-primary-1;
    color: $color-primary;

    &.dark {
      background-color: $color-primary;
      color: #fff;
    }

    > .close:hover {
      background-color: $color-primary;
      color: #fff;
    }
  }

  &.success {
    border-color: $color-success;
    background-color: $color-success-1;
    color: $color-success;

    &.dark {
      background-color: $color-success;
      color: #fff;
    }

    > .close:hover {
      background-color: $color-success;
      color: #fff;
    }
  }

  &.warning {
    border-color: $color-warning;
    background-color: $color-warning-1;
    color: $color-warning;

    &.dark {
      background-color: $color-warning;
      color: #fff;
    }

    > .close:hover {
      background-color: $color-warning;
      color: #fff;
    }
  }

  &.error {
    border-color: $color-error;
    background-color: $color-error-1;
    color: $color-error;

    &.dark {
      background-color: $color-error;
      color: #fff;
    }

    > .close:hover {
      background-color: $color-error;
      color: #fff;
    }
  }

  > .content {
    max-width: 200px;
    @extend .text_hide;
  }

  > .close {
    width: 14px;
    height: 14px;
    line-height: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    cursor: pointer;
  }
}
</style>
