<template>
  <div class="fx-progress" :class="[type]">
    <div class="progress-line">
      <div class="line" :style="lineStyle">
        <div class="inline" :style="inlineStyle">
          <span v-if="inside&&showText" class="mr10">{{ progressText }}</span>
        </div>
      </div>
      <div class="out-text" v-if="!inside&&showText">{{ progressText }}</div>
    </div>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, PropType, ref } from 'vue'
import { useModelWatch } from '@/base/hooks'
import { ComponentTypes, componentTypesArr } from '@/base/types'

type ProgressFormat = (val: number) => number | string
type ProgressColor = ({ percentage: number, color: string }[]) | string | ((val: number) => string)

interface ProgressProps {
  modelValue: number,
  strokeWidth: number,
  type: ComponentTypes,
  format: ProgressFormat,
  inside: boolean,
  color: ProgressColor,
  showText: boolean
}

const insideHeight = 18

export default defineComponent({
  name: 'FProgress',
  props: {
    modelValue: {
      type: Number as PropType<number>,
      default: 0,
      required: true,
      validator (val: number): boolean {
        return val >= 0 && val <= 100
      }
    },
    // 进度条宽度
    strokeWidth: {
      type: Number as PropType<number>,
      default: 0
    },
    // 类型
    type: {
      type: String as PropType<ComponentTypes>,
      default: 'primary',
      validator (val: string): boolean {
        return componentTypesArr.includes(val)
      }
    },
    // 格式化
    format: {
      type: Function as PropType<ProgressFormat>,
      default: null
    },
    // 进度条内部显示文字
    inside: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 颜色集合
    color: {
      type: [Array, Function, String] as PropType<ProgressColor>,
      default: () => []
    },
    // 是否显示文字
    showText: {
      type: Boolean as PropType<boolean>,
      default: true
    }
  },
  setup (props: ProgressProps) {
    const progressValue = ref<number>(props.modelValue)
    useModelWatch(() => props.modelValue, progressValue)

    // 默认样式
    const lineStyle = computed(() => {
      const height = props.strokeWidth ? props.strokeWidth : props.inside ? insideHeight : 6
      return {
        height: `${height}px`,
        lineHeight: `${height}px`,
        borderRadius: `${height / 2}px`
      }
    })
    // 默认样式
    const inlineStyle = computed(() => {
      let color: string | null = null
      if (props.color instanceof Array) {
        props.color.forEach(item => {
          if (progressValue.value >= item.percentage) color = item.color
        })
      }
      if (typeof props.color === 'string') {
        color = props.color
      }
      if (typeof props.color === 'function') {
        color = props.color(progressValue.value)
      }
      const height = props.strokeWidth ? props.strokeWidth : props.inside ? insideHeight : 6
      return {
        backgroundColor: color ?? null,
        width: `${progressValue.value}%`,
        borderRadius: `${height / 2}px`
      }
    })

    // 显示进度值
    const progressText = computed(() => {
      if (props.format && typeof props.format === 'function') return props.format(progressValue.value)
      return `${progressValue.value}%`
    })

    return {
      progressValue,
      progressText,
      lineStyle,
      inlineStyle
    }
  }
})
</script>

<style scoped lang="scss">
@import "../scss/base";

.fx-progress {
  width: 350px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;

  &.primary {
    > .progress-line {
      > .line > .inline {
        background-color: $color-primary;
      }
    }
  }

  &.success {
    > .progress-line {
      > .line > .inline {
        background-color: $color-success;
      }
    }
  }

  &.warning {
    > .progress-line {
      > .line > .inline {
        background-color: $color-warning;
      }
    }
  }

  &.error {
    > .progress-line {
      > .line > .inline {
        background-color: $color-error;
      }
    }
  }

  > .progress-line {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    > .line {
      width: 100%;
      background-color: #ebeef5;
      position: relative;
      overflow: hidden;

      > .inline {
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        color: #fff;
        transition: all 0.15s;
        font-size: 12px;
      }
    }

    > .out-text {
      margin-left: 10px;
      width: 40px;
      min-width: 40px;
      text-align: center;
      @extend .text_hide;
    }
  }
}
</style>
