<template>
  <div class="fx-checkbox-group" :class="{'button-group':isButton}">
    <slot></slot>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, inject, onMounted, PropType, provide, reactive, SetupContext, watch } from 'vue'
import mitt, { Emitter } from 'mitt'
import { useIsDefaultSlotName } from '@/base/hooks'
import { ComponentSize, componentSizeArr } from '@/base/types'

interface CheckboxGroupProps {
  modelValue: string[],
  disabled: boolean,
  max: number,
  min: number,
  size: ComponentSize
}

export default defineComponent({
  name: 'checkboxGroup',
  props: {
    // v-model
    modelValue: {
      type: Array as PropType<string[]>,
      required: true,
      default: () => []
    },
    // 禁用
    disabled: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 最大选中个数
    max: {
      type: Number as PropType<number>,
      default: 999999
    },
    // 最小选中个数
    min: {
      type: Number as PropType<number>,
      default: 0
    },
    // 大小
    size: {
      type: String as PropType<ComponentSize>,
      default: 'default',
      validator (val: string): boolean {
        return componentSizeArr.includes(val)
      }
    }
  },
  emits: ['update:modelValue', 'change'],
  setup (props: CheckboxGroupProps, ctx: SetupContext) {
    // 向子级传送数据
    const checkboxMitt: Emitter = mitt()
    // 当前选中数组
    let checkboxGroupValue = reactive<string[]>(props.modelValue)
    // 是否需要按钮border
    const isButton = useIsDefaultSlotName('FCheckboxButton')
    // 接收formItem mitt 监听器
    const formItemMitt = inject<Emitter['emit'] | undefined>('formItemMitt', undefined)

    // 当前选中
    provide('checkboxGroupValue', computed(() => props.modelValue))
    // 禁用
    provide('checkboxGroupDisabled', props.disabled)
    // 禁用
    provide('checkboxGroupSize', props.size)
    // 数据传送监听
    provide('checkboxGroupMittEmit', checkboxMitt.emit)
    // 选中个数限制
    provide('checkboxGroupNumber', {
      max: props.max,
      min: props.min
    })

    const handleCheckboxMitt = (val: string | undefined): void => {
      if (!val) return
      const checkedIndex: number = checkboxGroupValue.findIndex((item: string) => item === val)
      // 当选择少于最小选择数3时 触发`
      if (checkboxGroupValue.length <= props.min && checkboxGroupValue[checkedIndex] === val) return
      if (checkedIndex < 0) {
        checkboxGroupValue.push(val)
      } else {
        checkboxGroupValue.splice(checkedIndex, 1)
      }
    }

    watch(() => props.modelValue, (val: string[]): void => {
      checkboxGroupValue = val
    })
    watch(checkboxGroupValue, (val: string[]): void => {
      ctx.emit('update:modelValue', val)
      ctx.emit('change', val)
      // 发送当前的数据给formItem
      if (formItemMitt) formItemMitt('formItemValue', val)
    })

    onMounted(() => {
      checkboxMitt.on<string | undefined>('checkboxMitt', handleCheckboxMitt)
      // 发送当前的数据给formItem
      if (formItemMitt) formItemMitt('formItemValue', checkboxGroupValue)
    })

    return {
      isButton,
      checkboxGroupValue
    }
  }
})
</script>

<style scoped lang="scss">
@import "../scss/base";

.fx-checkbox-group {
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;

  &.button-group {
    border: 1px solid $border-color-default;
    border-radius: $border-radius-default;
    overflow: hidden;
  }
}
</style>
