<template>
  <ul class="fx-tree">
    <li class="fx-tree-content" v-for="item in treeValue" :key="item.fxTreeId">
      <div class="tree-text" @click="treeClick(item)">
        <em v-if="item.children&&item.children.length" :class="{open:(item.showChildren)}">
          <i class="iconfont icon-to-bottom fs10"/>
        </em>
        <span>{{ item.label }} <em class="success">{{ item.fxTreeId }}</em></span>
      </div>
      <template v-if="item.children&&item.children.length">
        <fx-tree v-show="item.showChildren" :data="item.children"></fx-tree>
      </template>
    </li>
  </ul>
</template>

<script lang="ts">
import { defineComponent, PropType } from 'vue'

interface TreeData {
  label: string,
  fxTreeId?: string,
  showChildren?: boolean,
  children?: TreeData[],

  [propsName: string]: unknown
}

interface TreeProps {
  data: TreeData[]
}

export default defineComponent({
  name: 'FTree',
  props: {
    data: {
      type: Array as PropType<TreeData[]>,
      required: true
    }
  },
  setup (props: TreeProps) {
    // 给数据添加id属性
    const addTreeId = (arr: TreeData[], id = ''): TreeData[] => {
      arr.forEach((item, index) => {
        item.fxTreeId = `${index + 1}${id === '' ? '' : '-' + id}`
        if (item.children && item.children.length) {
          item.showChildren = false
          addTreeId(item.children, item.fxTreeId)
        }
      })
      return arr
    }
    // 树 数据
    const treeValue: TreeData[] = addTreeId(props.data)

    // 点击事件
    const treeClick = (data: TreeData): void => {
      if (data.children && data.children.length) {
        data.showChildren = !data.showChildren
      }
    }

    return {
      treeValue,
      treeClick
    }
  }
})
</script>

<style scoped lang="scss">
@import "../scss/base";

.fx-tree {
  > li {
    padding-left: 10px;

    .tree-text {
      cursor: pointer;
      display: flex;
      align-items: center;

      > em {
        color: $font-color-lowest;
        transition: all 0.15s;
        transform-origin: center center;
        transform: rotate(-90deg);

        &.open {
          transform: rotate(0deg);
        }
      }

      > span {
        margin-left: 5px;
      }
    }
  }
}
</style>
