<template>
  <menu class="menu" :class="{border:border}" v-bind="$attrs">
    <slot></slot>
  </menu>
</template>

<script lang="ts">
import { defineComponent, PropType, provide, computed, onMounted, ref, watch } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import mitt, { Emitter } from 'mitt'
import { MenuMitt } from '@/base/menu/menuTypes'

interface MenuProps {
  defaultActive: string,
  border: boolean,
  router: boolean
}

export default defineComponent({
  name: 'FMenu',
  inheritAttrs: true,
  props: {
    // 默认选中
    defaultActive: {
      type: String as PropType<string>,
      required: true
    },
    // 是否需要边框
    border: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    router: {
      type: Boolean as PropType<boolean>,
      default: false
    }
  },
  setup (props: MenuProps) {
    const Route = useRoute()
    const Router = useRouter()
    // 菜单数据监听
    const menuMitt: Emitter = mitt()
    // 当前选中
    const currentIndex = ref<string>(props.defaultActive)
    // 处理子菜单点击事件
    const handleMenuItemClick = (event: string | undefined): void => {
      if (event) {
        currentIndex.value = event
        if (props.router) Router.push(event)
      }
    }

    watch(() => Route.path, (val) => {
      currentIndex.value = val
    })

    // 全子孙组件传递当前选中
    provide('defaultActive', computed<string>(() => currentIndex.value))
    // 全组件发布更新选中数据方式
    provide<MenuMitt>('menuMitt', {
      menuMittEmit: menuMitt.emit
    })

    onMounted(() => {
      // 监听菜单子组件点击事件
      menuMitt.on('menuItemClick', handleMenuItemClick)
      // 初始化跳转默认路由
      if (props.router && props.defaultActive !== '') Router.push(props.defaultActive)
    })

    return {}
  }
})
</script>

<style scoped lang="scss">
@import "../scss/base";

.menu {
  color: $font-color-important;
  width: 160px;
  height: 100%;
  overflow-y: auto;

  &.border {
    border-right: 1px solid $border-color-default;
  }
}
</style>
