
import { defineComponent, inject, onMounted, PropType, ref, SetupContext } from 'vue'
import { ComponentSize, componentSizeArr } from '@/base/types'
import { Emitter } from 'mitt'
import { useModelWatch } from '@/base/hooks'

interface RadioProps {
  modelValue: string | number,
  label: string | number,
  disabled: boolean,
  size: ComponentSize,
  border: boolean
}

export default defineComponent({
  name: 'FRadio',
  props: {
    // v-model
    modelValue: {
      type: [String, Number] as PropType<string | number>,
      default: ''
    },
    // 选项
    label: {
      type: [String, Number] as PropType<string | number>,
      default: '',
      required: true
    },
    // 禁用
    disabled: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 大小
    size: {
      type: String as PropType<ComponentSize>,
      default: 'default',
      validator (val: string): boolean {
        return componentSizeArr.includes(val)
      }
    },
    // 边框
    border: {
      type: Boolean as PropType<boolean>,
      default: false
    }
  },
  emits: ['update:modelValue', 'change'],
  setup (props: RadioProps, ctx: SetupContext) {
    // 获取来自radioGroup emit
    const radioGroupEmit = inject<Emitter['emit'] | undefined>('radioGroupMittEmit', undefined)
    // 获取来自radioGroup 选中
    const radioGroupValue = inject('radioGroupValue', { value: '' })
    // 获取来自radioGroup 禁用
    const radioGroupDisabled = inject('radioGroupDisabled', false)
    // 获取来自radioGroup 大小
    const radioGroupSize = inject('radioGroupSize', '')
    // 当前选中
    const currentRadioValue = ref<string | number>(props.modelValue)
    // 数据双向绑定hook
    useModelWatch<string | number>(() => props.modelValue, currentRadioValue)
    // 接收formItem mitt 监听器
    const formItemMitt = inject<Emitter['emit'] | undefined>('formItemMitt', undefined)

    // 单选点击
    const radioClick = (): void => {
      if (props.disabled || radioGroupDisabled) return
      // 当存在radioGroup父级时生效
      if (radioGroupEmit) radioGroupEmit('radioGroupClick', props.label)
      currentRadioValue.value = props.label
      ctx.emit('change', props.label)
      // 发送当前的数据给formItem
      if (formItemMitt) formItemMitt('formItemValue', currentRadioValue.value)
    }

    onMounted(() => {
      // 发送当前的数据给formItem
      if (formItemMitt) formItemMitt('formItemValue', currentRadioValue.value)
    })

    return {
      radioGroupSize,
      radioGroupValue,
      currentRadioValue,
      radioGroupDisabled,
      radioClick
    }
  }
})
