<template>
  <div class="fx-loading" :style="loadingStyle">
    <div class="fx-loading-center">
      <ul class="fx-loading-animation">
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
      <div class="fx-loading-text" v-if="text&&text!==''">{{ text }}</div>
    </div>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, PropType } from 'vue'

export interface LoadingProps {
  text: string;
  bgColor: string;
  textColor: string;
}

export default defineComponent({
  name: 'FLoading',
  props: {
    text: {
      type: String as PropType<string>,
      default: null
    },
    bgColor: {
      type: String as PropType<string>,
      default: 'rgba(0, 0, 0, .8)'
    },
    textColor: {
      type: String as PropType<string>,
      default: '#fff'
    }
  },
  setup (props: LoadingProps) {
    const loadingStyle = computed(() => {
      return {
        backgroundColor: props.bgColor,
        color: props.textColor
      }
    })

    return {
      loadingStyle
    }
  }
})
</script>

<style scoped lang="scss">
@import "../../scss/base";

@keyframes spin {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}

.fx-loading {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;

  > .fx-loading-center {
    position: absolute;
    left: 50%;
    top: 50%;
    transform-origin: center center;
    transform: translateX(-50%) translateY(-50%);

    > .fx-loading-animation {
      width: 100%;
      min-width: 60px;
      height: 60px;
      position: relative;
      transform: rotate(-45deg);

      > li {
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        border-radius: 50%;
        border: 2px solid transparent;
        animation: spin 1.5s cubic-bezier(.175, .885, .32, 1.275) infinite;

        @for $i from 1 through 5 {
          &:nth-of-type(#{$i}) {
            width: 10px * $i;
            height: 10px * $i;
            animation-delay: -100ms*$i;
            @if $i==5 {
              border-top-color: #eb4747;
              border-right-color: #eb4747;
            }
            @if $i==4 {
              border-top-color: #ebeb47;
              border-right-color: #ebeb47;
            }
            @if $i==3 {
              border-top-color: #47eb47;
              border-right-color: #47eb47;
            }
            @if $i==2 {
              border-top-color: #47ebeb;
              border-right-color: #47ebeb;
            }
            @if $i==1 {
              border-top-color: #4747eb;
              border-right-color: #4747eb;
            }
          }
        }
      }
    }

    > .fx-loading-text {
      height: 24px;
      line-height: 24px;
      white-space: nowrap;
      text-align: center;
    }
  }
}
</style>
