
import { defineComponent, onBeforeUnmount, onMounted, PropType, ref } from 'vue'
import fxButton from '@/base/button/button.vue'
import mitt, { Emitter } from 'mitt'

export interface ConfirmProps {
  title: string;
  content: string;
  defineText: string;
  cancelText: string;
  showClose: boolean;
  showTitle: boolean;
  closeOnModal: boolean;
  closeOnEscape: boolean;
  showCancelButton: boolean;
  showDefineButton: boolean;
  roundButton: boolean;
}

export default defineComponent({
  name: 'FConfirm',
  components: {
    fxButton
  },
  props: {
    // 标题
    title: {
      type: String as PropType<string>,
      default: '提示'
    },
    // 内容
    content: {
      type: String as PropType<string>,
      default: null
    },
    // 确定按钮文字
    defineText: {
      type: String as PropType<string>,
      default: '确定'
    },
    // 取消按钮文字
    cancelText: {
      type: String as PropType<string>,
      default: '取消'
    },
    // 是否显示关闭按钮
    showClose: {
      type: Boolean as PropType<boolean>,
      default: true
    },
    // 是否显示标题
    showTitle: {
      type: Boolean as PropType<boolean>,
      default: true
    },
    // 是否通过遮罩层关闭
    closeOnModal: {
      type: Boolean as PropType<boolean>,
      default: true
    },
    // 是否通过ESC关闭
    closeOnEscape: {
      type: Boolean as PropType<boolean>,
      default: true
    },
    // 是否显示取消按钮
    showCancelButton: {
      type: Boolean as PropType<boolean>,
      default: true
    },
    // 是否显示确定按钮
    showDefineButton: {
      type: Boolean as PropType<boolean>,
      default: true
    },
    // 圆角按钮
    roundButton: {
      type: Boolean as PropType<boolean>,
      default: false
    }
  },
  setup (props: ConfirmProps) {
    const confirmMitt: Emitter = mitt()
    // 是否关闭
    const confirmClose = ref<boolean>(false)
    // 是否关闭
    const defineLoading = ref<boolean>(false)
    // 是否关闭
    const cancelLoading = ref<boolean>(false)
    // 取消/关闭点击
    const cancelClick = (type: 'close' | 'cancel', from: 'btn' | 'modal' | 'icon' | 'esc'): void => {
      if (!props.closeOnModal && from === 'modal') return
      if (!props.closeOnEscape && from === 'esc') return
      confirmMitt.emit('close', type)
    }
    // 确定关闭
    const defineClick = (): void => {
      confirmMitt.emit('define', 'define')
    }
    // 点击ESC退出
    const handleEscape = (): void => {
      cancelClick('close', 'esc')
    }
    // 关闭事件
    const closeConfirm = (): void => {
      confirmClose.value = true
    }
    // 设置取消的loading
    const setCancelLoading = (val: boolean): void => {
      cancelLoading.value = val
    }
    // 设置确定的loading
    const setDefineLoading = (val: boolean): void => {
      defineLoading.value = val
    }

    onMounted(() => {
      document.addEventListener('keydown', handleEscape, false)
    })
    onBeforeUnmount(() => {
      document.removeEventListener('keydown', handleEscape, false)
    })

    return {
      confirmMitt,
      confirmClose,
      defineLoading,
      cancelLoading,
      setCancelLoading,
      setDefineLoading,
      closeConfirm,
      cancelClick,
      defineClick
    }
  }
})
