import { App } from 'vue'
import initDirective from '@/base/directive'
import GlobalLoading, { GlobalLoadingProps } from './directive/loading/globalLoading'
import { Message } from './directive/message/globalMessage'
import { Notify } from './directive/notify/globalNotify'
import { Confirm } from '@/base/directive/confirm/globalConfirm'

const requireComponent = require.context('./', true, /.vue$/)

export const FxLoading = (props?: Partial<GlobalLoadingProps>): GlobalLoading => new GlobalLoading(props)
export const FxMessage = Message
export const FxNotify = Notify
export const FxConfirm = Confirm

export const globalComponents = (app: App): void => {
  requireComponent.keys().forEach((filePath: string) => {
    const file: string = filePath.split('/').pop() || ''
    if (file === '' || /directive/.test(file)) return
    const fileName: string = file.replace(/\.\w+$/, '')
    const toUpper = fileName.slice(0, 1).toUpperCase() + fileName.slice(1)
    const componentConfig = requireComponent(filePath)
    app.component(`fx${toUpper}`, componentConfig.default)
  })
  initDirective(app)
}
