<template>
  <div class="fx-input" :class="[size]" v-bind="$attrs">
    <template v-if="type!=='textarea'">
      <div class="prefix-icon" v-if="prefixIcon!==''&&!$slots.prefix">
        <i class="fs14 iconfont" :class="prefixIcon"/>
      </div>
      <div class="prefix-icon" v-if="$slots.prefix">
        <slot name="prefix"></slot>
      </div>
      <input v-model="inputValue" :type="inputType" :style="{width:`${$attrs.width}px`}"
             @blur="inputBlur" ref="inputDom" @focus="$emit('focus',inputValue)"
             :disabled="$attrs.disabled" :placeholder="$attrs.placeholder"
             :class="{round,prefix:(prefixIcon===''&&!$slots.prefix),
             suffix:(suffixIcon===''&&!clear&&!password&&!$slots.suffix)}">
      <template v-if="!$slots.suffix">
        <div class="suffix-icon" v-if="clear&&inputValue!==''" @click="clearInputClick">
          <i class="fs12 iconfont icon-multiply_circle"/>
        </div>
        <div class="suffix-icon" v-else-if="password" @click="showHidePasswordClick">
          <i class="fs12 iconfont icon-open-eyes"/>
        </div>
        <div class="suffix-icon" v-else-if="suffixIcon!==''">
          <i class="fs14 iconfont" :class="suffixIcon"/>
        </div>
      </template>
      <div class="suffix-icon" v-else>
        <slot name="suffix"></slot>
      </div>
    </template>
    <div class="textarea" v-else>
      <textarea v-bind="$attrs" :style="{resize:$attrs.resize}"></textarea>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, inject, onMounted, PropType, ref, SetupContext } from 'vue'
import { ComponentSize, componentSizeArr } from '@/base/types'
import { useModelWatch } from '@/base/hooks'
import { Emitter } from 'mitt'

interface InputProps {
  modelValue: string,
  type: string,
  size: ComponentSize,
  prefixIcon: string,
  suffixIcon: string,
  clear: boolean,
  password: boolean,
  round: boolean,
  autoFocus: boolean,
}

export default defineComponent({
  name: 'FInput',
  props: {
    // v-model
    modelValue: {
      type: String as PropType<string>,
      default: '',
      required: true
    },
    // 原生type
    type: {
      type: String as PropType<string>,
      default: 'text'
    },
    // 大小
    size: {
      type: String as PropType<ComponentSize>,
      default: 'default',
      validator (value: string): boolean {
        return componentSizeArr.includes(value)
      }
    },
    // 前部icon
    prefixIcon: {
      type: String as PropType<string>,
      default: ''
    },
    // 后部icon
    suffixIcon: {
      type: String as PropType<string>,
      default: ''
    },
    // 清空
    clear: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 显示密码
    password: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 圆角
    round: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 是否自动聚焦
    autoFocus: {
      type: Boolean as PropType<boolean>,
      default: false
    }
  },
  emits: ['update:modelValue', 'input', 'blur', 'focus'],
  setup (props: InputProps, ctx: SetupContext) {
    // 当前输入框内容
    const inputValue = ref<string>(props.modelValue)
    useModelWatch(() => props.modelValue, inputValue, ['update:modelValue', 'input'])
    // input
    const inputDom = ref<HTMLElement | undefined>(undefined)
    // 当前输入框类型
    const inputType = ref<string>(props.type)
    // 接收formItem mitt 监听器
    const formItemMitt = inject<Emitter['emit'] | undefined>('formItemMitt', undefined)

    // 清空输入框
    const clearInputClick = (): void => {
      inputValue.value = ''
      if (inputDom.value) inputDom.value.focus()
    }
    // 显示/隐藏输入框
    const showHidePasswordClick = (): void => {
      inputType.value = inputType.value === 'password' ? 'text' : 'password'
    }
    // 失焦
    const inputBlur = (): void => {
      if (formItemMitt) formItemMitt('formItemBlur', inputValue.value)
      ctx.emit('blur', inputValue.value)
    }

    onMounted(() => {
      // 当开启显示密码时 输入框自动转为密码类型
      if (props.password) inputType.value = 'password'
      // 发送当前的数据给formItem
      if (formItemMitt) formItemMitt('formItemValue', inputValue.value)
      // 是否自动聚焦
      setTimeout(() => {
        if (props.autoFocus && inputDom.value) inputDom.value.focus()
      })
    })

    return {
      inputValue,
      inputType,
      inputDom,
      inputBlur,
      clearInputClick,
      showHidePasswordClick
    }
  }
})
</script>

<style scoped lang="scss">
@import "../scss/base";

.fx-input {
  box-sizing: border-box;
  display: inline-flex;
  transition: all 0.3s;
  position: relative;

  &.large {
    height: $height-large;

    > input {
      padding-right: $height-large;
      padding-left: $height-large;
      line-height: calc(#{$height-large} - 2px);

      &.round {
        border-radius: calc(#{$height-large} / 2);
      }
    }

    > .suffix-icon, > .prefix-icon {
      width: calc(#{$height-large} - 2px);
      height: calc(#{$height-large} - 2px);
      line-height: calc(#{$height-large} - 2px);
    }
  }

  &.middle {
    height: $height-middle;

    > input {
      padding-right: $height-middle;
      padding-left: $height-middle;
      line-height: calc(#{$height-middle} - 2px);

      &.round {
        border-radius: calc(#{$height-middle} / 2);
      }
    }

    > .suffix-icon, > .prefix-icon {
      width: calc(#{$height-middle} - 2px);
      height: calc(#{$height-middle} - 2px);
      line-height: calc(#{$height-middle} - 2px);
    }
  }

  &.default {
    height: $height-default;

    > input {
      padding-right: $height-default;
      padding-left: $height-default;
      line-height: calc(#{$height-default} - 2px);

      &.round {
        border-radius: calc(#{$height-default} / 2);
      }
    }

    > .suffix-icon, > .prefix-icon {
      width: calc(#{$height-default} - 2px);
      height: calc(#{$height-default} - 2px);
      line-height: calc(#{$height-default} - 2px);
    }
  }

  &.mini {
    height: $height-mini;

    > input {
      padding-right: $height-mini;
      padding-left: $height-mini;
      line-height: calc(#{$height-mini} - 2px);

      &.round {
        border-radius: calc(#{$height-mini} / 2);
      }
    }

    > .suffix-icon, > .prefix-icon {
      width: calc(#{$height-mini} - 2px);
      height: calc(#{$height-mini} - 2px);
      line-height: calc(#{$height-mini} - 2px);
    }
  }

  > input {
    box-sizing: border-box;
    width: $input-width-default;
    height: 100%;
    outline: none;
    border: 1px solid $border-color-secondary;
    border-radius: $border-radius;

    &:hover {
      border-color: $border-color-important;
    }

    &:focus {
      border: 1px solid $color-primary;
    }

    &.prefix {
      padding-left: 10px;
    }

    &.suffix {
      padding-right: 10px;
    }

    &:disabled {
      background-color: #f5f7fa;
      cursor: not-allowed;
    }
  }

  > .suffix-icon, > .prefix-icon {
    user-select: none;
    cursor: pointer;
    text-align: center;
    position: absolute;
    top: 1px;
    color: $font-color-lowest;

    > .icon-open-eyes, > .icon-multiply_circle {
      &:hover {
        color: $font-color-secondary;
      }
    }
  }

  > .suffix-icon {
    right: 0;
  }

  > .prefix-icon {
    left: 0;
  }

  > .textarea {
    > textarea {
      width: 400px;
      height: 100%;
      line-height: 1.5;
      box-sizing: border-box;
      padding: 5px 15px;
      border: 1px solid $border-color-secondary;
      border-radius: $border-radius;
      resize: vertical;
      outline: none;

      &:hover {
        border-color: $border-color-important;
      }

      &:focus {
        border: 1px solid $color-primary;
      }

      &::-webkit-input-placeholder {
        color: #abb4c3;
      }
    }
  }
}
</style>
