<template>
  <div class="fx-dialog" :class="{'dialog-leave':dialogClose}" v-if="currentVisible">
    <div class="fx-dialog-inner" :style="{top:top,width:width}">
      <div class="fx-dialog-title" v-if="$slots.title||title!==''">
        <slot name="title">{{ title }}</slot>
        <div class="close" v-if="showClose" @click="closeDialog('close')">
          <i class="iconfont icon-multiply fs10"/>
        </div>
      </div>
      <div class="fx-dialog-content">
        <slot></slot>
      </div>
      <div class="fx-dialog-footer" v-if="$slots.footer">
        <slot name="footer"></slot>
      </div>
    </div>
    <div class="fx-dialog-modal" @click="closeDialog('modal')"></div>
  </div>
</template>

<script lang="ts">
import { defineComponent, onBeforeMount, onBeforeUnmount, PropType, ref, SetupContext, watch } from 'vue'

export type DialogBeforeClose = (cb: () => void) => void

interface DialogProps {
  visible: boolean;
  title: string;
  top: string;
  width: string;
  showClose: boolean;
  closeOnModal: boolean;
  closeOnEscape: boolean;
  beforeClose: DialogBeforeClose;
}

export default defineComponent({
  name: 'FDialog',
  props: {
    // 显示
    visible: {
      type: Boolean as PropType<boolean>,
      default: false,
      required: true
    },
    // 距离顶部距离
    title: {
      type: String as PropType<string>,
      default: '提示'
    },
    // 距离顶部距离
    top: {
      type: String as PropType<string>,
      default: '10vh'
    },
    // 距离顶部距离
    width: {
      type: String as PropType<string>,
      default: '50vw'
    },
    // 是否显示关闭按钮
    showClose: {
      type: Boolean as PropType<boolean>,
      default: true
    },
    // 是否点击模态窗关闭
    closeOnModal: {
      type: Boolean as PropType<boolean>,
      default: true
    },
    // 是否Esc关闭
    closeOnEscape: {
      type: Boolean as PropType<boolean>,
      default: true
    },
    // 关闭前
    beforeClose: {
      type: Function as PropType<DialogBeforeClose>,
      default: null
    }
  },
  emits: ['update:visible'],
  setup (props: DialogProps, ctx: SetupContext) {
    const dialogClose = ref<boolean>(false)
    const currentVisible = ref<boolean>(props.visible)

    // 关闭时的动效
    const closeDialogAnimation = () => {
      dialogClose.value = true
      setTimeout(() => {
        currentVisible.value = false
        dialogClose.value = false
      }, 300)
    }
    // 关闭弹窗
    const closeDialog = (type: 'modal' | 'esc' | 'close' | 'default' = 'default') => {
      if (type === 'modal' && !props.closeOnModal) return
      if (props.beforeClose && (type === 'modal' || type === 'esc' || type === 'close')) {
        props.beforeClose(() => {
          closeDialogAnimation()
        })
        return
      }
      closeDialogAnimation()
    }
    // Esc关闭
    const handleEscClose = (ev: KeyboardEvent) => {
      if (ev.code === 'Escape' && props.closeOnEscape) closeDialog('esc')
    }

    watch(() => props.visible, (val: boolean): void => {
      if (!val) {
        closeDialog()
      } else {
        currentVisible.value = true
      }
    })
    watch(currentVisible, (val: boolean) => {
      ctx.emit('update:visible', val)
    })

    onBeforeMount(() => {
      document.addEventListener('keydown', handleEscClose)
    })
    onBeforeUnmount(() => {
      document.removeEventListener('keydown', handleEscClose)
    })

    return {
      dialogClose,
      currentVisible,
      closeDialog
    }
  }
})
</script>

<style scoped lang="scss">
@import "../scss/base";

.fx-dialog {
  position: fixed;
  left: 0;
  top: 0;
  z-index: $z-index-modal-default;
  width: 100vw;
  height: 100vh;

  &.dialog-leave {
    > .fx-dialog-inner {
      animation: pop-ups-leave 0.3s forwards;
    }

    > .fx-dialog-modal {
      animation: modal-leave 0.3s forwards;
    }
  }

  > .fx-dialog-inner {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    z-index: 10;
    background-color: #fff;
    border-radius: $border-radius-default;
    animation: pop-ups-enter 0.3s;

    > .fx-dialog-title {
      padding: 20px 30px 10px 20px;
      font-size: 16px;
      position: relative;
      display: flex;
      align-items: center;

      > .close {
        position: absolute;
        right: 20px;
        top: 20px;
        cursor: pointer;
      }
    }

    > .fx-dialog-content {
      padding: 10px 20px;
    }

    > .fx-dialog-footer {
      padding: 10px 20px 20px;
    }
  }

  > .fx-dialog-modal {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: $modal-color;
    animation: modal-enter 0.3s;
  }
}
</style>
