<template>
  <div class="fx-radio-button" @click="radioButtonClick"
       :class="[radioGroupSize!==''?radioGroupSize:size,{active:label===radioButtonValue,disabled:(radioGroupDisabled||disabled)}]">
    <input type="radio" hidden :name="radioButtonValue" :value="label">
    <slot></slot>
  </div>
</template>

<script lang="ts">
import { defineComponent, inject, PropType } from 'vue'
import { Emitter } from 'mitt'
import { ComponentSize, componentSizeArr } from '@/base/types'

interface RadioButton {
  label: string | number,
  size: ComponentSize,
  disabled: boolean
}

export default defineComponent({
  name: 'FRadioButton',
  props: {
    // 选项
    label: {
      type: [String, Number] as PropType<string | number>,
      default: '',
      required: true
    },
    // 大小
    size: {
      type: String as PropType<ComponentSize>,
      default: 'default',
      validator (val: string): boolean {
        return componentSizeArr.includes(val)
      }
    },
    // 禁用
    disabled: {
      type: Boolean as PropType<boolean>,
      default: false
    }
  },
  setup (props: RadioButton) {
    // 获取来自radioGroup emit
    const radioGroupEmit = inject<Emitter['emit'] | undefined>('radioGroupMittEmit', undefined)
    // 获取来自radioGroup 选中
    const radioButtonValue = inject('radioGroupValue', { value: '' })
    // 取货来自radioGroup 禁用
    const radioGroupDisabled = inject('radioGroupDisabled', true)
    // 取货来自radioGroup 禁用
    const radioGroupSize = inject('radioGroupSize', '')

    // 单选按钮点击
    const radioButtonClick = (): void => {
      if (props.disabled || radioGroupDisabled) return
      // 当存在radioGroup父级时生效
      if (radioGroupEmit) radioGroupEmit('radioGroupClick', props.label)
    }

    return {
      radioGroupSize,
      radioButtonValue,
      radioGroupDisabled,
      radioButtonClick
    }
  }
})
</script>

<style scoped lang="scss">
@import "../scss/base";

.fx-radio-button {
  user-select: none;
  cursor: pointer;
  display: inline-block;
  background-color: #fff;
  border-right: 1px solid $border-color-default;
  transition: all 0.15s ease-in;

  &:last-of-type {
    border-right: none;
  }

  &:hover {
    color: $color-primary;
  }

  &.active {
    background-color: $color-primary;
    color: #fff;
  }

  &.disabled {
    cursor: not-allowed;
    color: $font-color-lowest;

    &.active {
      background-color: $font-color-lowest;
      color: #fff;
    }
  }

  &.large {
    height: $height-large;
    line-height: $height-large;
    padding: 0 20px;
  }

  &.middle {
    height: $height-middle;
    line-height: $height-middle;
    padding: 0 20px;
  }

  &.default {
    height: $height-default;
    line-height: $height-default;
    padding: 0 15px;
  }

  &.mini {
    height: $height-mini;
    line-height: $height-mini;
    padding: 0 15px;
  }
}
</style>
