<template>
  <div class="submenu">
    <div class="title" :class="{active:index===defaultActive}" @click="submenuClick">
      <div class="text">
        <slot name="title">{{ title }}</slot>
      </div>
      <span :class="{open:submenuOpen}">
        <i class="iconfont icon-chevron_down fs10"/>
      </span>
    </div>
    <div class="content" v-if="submenuOpen">
      <slot name="default"></slot>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, inject, PropType } from 'vue'

export default defineComponent({
  name: 'submenu',
  props: {
    title: {
      type: String as PropType<string>,
      default: ''
    },
    index: {
      type: String as PropType<string>,
      required: true
    }
  },
  setup () {
    const defaultActive = inject<string>('defaultActive')

    const submenuOpen = ref<boolean>(false)

    const submenuClick = (): void => {
      submenuOpen.value = !submenuOpen.value
    }

    return {
      submenuOpen,
      defaultActive,
      submenuClick
    }
  }
})
</script>

<style scoped lang="scss">
@import "../scss/base";

.submenu {
  > .title {
    box-sizing: border-box;
    width: 100%;
    height: $height-default;
    line-height: $height-default;
    cursor: pointer;
    font-size: 14px;
    color: $font-color-important;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;

    &:hover {
      background-color: $color-primary-1;
    }

    &.active {
      background-color: $color-primary-1;
      color: $color-primary;
    }

    > .text {
      width: calc(100% - 20px);
      @include text_hide(1);
    }

    > span {
      display: inline-block;
      width: 20px;
      min-width: 20px;
      text-align: center;
      transform-origin: center center;
      transition: all 0.3s;

      &.open {
        transform: rotate(180deg);
      }
    }
  }

  > .content {
    box-sizing: border-box;
    width: 100%;
    overflow: hidden;
    transform-origin: top center;
    transition: height 0.3s;
  }
}
</style>
