<template>
  <div class="fx-carousel" ref="carouselDom" :style="carouselStyle" @mouseenter="mouseenter"
       @mouseleave="initCarousel('toLeft')">
    <div class="fx-carousel-inner" ref="carouselInnerDom">
      <slot></slot>
    </div>
    <div class="fx-carousel-path" :class="[arrow]" v-if="arrow!=='never'" @click="pathClick('toRight')">
      <i class="iconfont icon-left fs10"/>
    </div>
    <div class="fx-carousel-path right" :class="[arrow]" v-if="arrow!=='never'" @click="pathClick('toLeft')">
      <i class="iconfont icon-right fs10"/>
    </div>
    <ul class="fx-carousel-dots" :class="[arrow]" v-if="isIndicator&&arrow!=='never'">
      <li v-for="(item,index) in currentChildren" :key="item"
          :class="{active:index===currentIndex}" @click="dotsClick(index)">
        <div class="step" :style="stepStyle(index)"></div>
      </li>
    </ul>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, onBeforeUnmount, onMounted, PropType, ref, watch } from 'vue'
import { addClassName, prefixStyle, removeClassName } from '@/base/utils'

interface CarouselProps {
  index: number;
  height: string | number;
  autoPlay: boolean;
  interval: number;
  isIndicator: boolean;
  indicatorAnimation: boolean;
  loop: boolean;
  arrow: 'always' | 'hover' | 'never';
}

export default defineComponent({
  name: 'FCarousel',
  props: {
    // 开始页
    index: {
      type: Number as PropType<number>,
      default: 0,
      validator (val: number): boolean {
        return val >= 0
      }
    },
    // 轮播图高度
    height: {
      type: [String, Number] as PropType<string | number>,
      required: true
    },
    // 是否自动播放
    autoPlay: {
      type: Boolean as PropType<boolean>,
      default: true
    },
    // 自动播放速度
    interval: {
      type: Number as PropType<number>,
      default: 3000
    },
    // 是否需要指示器
    isIndicator: {
      type: Boolean as PropType<boolean>,
      default: true
    },
    // 是否需要指示器动画
    indicatorAnimation: {
      type: Boolean as PropType<boolean>,
      default: true
    },
    // 控制器显示方式
    arrow: {
      type: String as PropType<'always' | 'hover' | 'never'>,
      default: 'always'
    },
    // 是否循环
    loop: {
      type: Boolean as PropType<boolean>,
      default: true
    }
  },
  emits: ['update:index', 'change'],
  setup (props: CarouselProps) {
    const currentIndex = ref<number>(props.index)
    const currentChildren = ref<HTMLElement[]>([])
    const carouselInnerDom = ref<HTMLElement>()
    const carouseTimer = ref()
    const carouselStyle = computed(() => {
      const isString = typeof props.height === 'string' && /px|rem|em|%/g.test(props.height)
      const height = isString ? props.height : `${props.height}px`
      return {
        height
      }
    })
    const stepStyle = (index: number): { [key: string]: string } | null => {
      if (index !== currentIndex.value || !props.indicatorAnimation) return null
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      const style: any = {
        backgroundColor: 'rgb(45, 183, 245)'
      }
      style[prefixStyle('transition')] = `width ${props.interval}ms linear`
      return style
    }
    const autoPlayer = () => {
      clearInterval(carouseTimer.value)
      if (props.autoPlay) {
        carouseTimer.value = setInterval(() => {
          pathClick('toLeft')
        }, props.interval)
      }
    }
    watch(() => props.autoPlay, () => {
      autoPlayer()
    })
    // 初始化轮播图
    const initCarousel = (path: 'toLeft' | 'toRight' = 'toLeft') => {
      if (!carouselInnerDom.value) return
      const children = Array.from(carouselInnerDom.value?.children) as HTMLElement[]
      currentChildren.value = children
      if (children.length <= 1) return
      if (currentIndex.value > children.length - 1) {
        currentIndex.value = props.loop ? 0 : children.length - 1
        if (!props.loop) clearInterval(carouseTimer.value)
      }
      if (currentIndex.value < 0) {
        currentIndex.value = props.loop ? children.length - 1 : 0
        if (!props.loop) clearInterval(carouseTimer.value)
      }
      if (children?.length) {
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        const transform = prefixStyle('transform') as any
        children.forEach((item: HTMLElement) => {
          removeClassName(item, 'active')
          item.style[transform] = path === 'toLeft' ? 'translateX(100%)' : 'translateX(-100%)'
        })
        addClassName(children[currentIndex.value], 'active')
        children[currentIndex.value].style[transform] = 'translateX(0)'
        if (children[currentIndex.value - 1] && path === 'toLeft') {
          addClassName(children[currentIndex.value - 1], 'active')
          children[currentIndex.value - 1].style[transform] = 'translateX(-100%)'
        } else if (path === 'toLeft') {
          addClassName(children[children.length - 1], 'active')
          children[children.length - 1].style[transform] = 'translateX(-100%)'
        }
        if (children[currentIndex.value + 1] && (path === 'toRight')) {
          addClassName(children[currentIndex.value + 1], 'active')
          children[currentIndex.value + 1].style[transform] = 'translateX(100%)'
        } else if (path === 'toRight') {
          addClassName(children[0], 'active')
          children[0].style[transform] = 'translateX(100%)'
        }
      }
      autoPlayer()
    }
    // 轮播点击
    const pathClick = (path: 'toLeft' | 'toRight') => {
      clearInterval(carouseTimer.value)
      if (path === 'toLeft') currentIndex.value++
      if (path === 'toRight') currentIndex.value--
      initCarousel(path)
    }
    // 轮播点点击
    const dotsClick = (index: number): void => {
      if (index > currentIndex.value) {
        currentIndex.value = index
        initCarousel('toLeft')
      } else {
        currentIndex.value = index
        initCarousel('toRight')
      }
    }
    // 轮播移入
    const mouseenter = (): void => {
      clearInterval(carouseTimer.value)
    }

    onMounted(() => {
      initCarousel()
    })
    onBeforeUnmount(() => {
      clearInterval(carouseTimer.value)
    })

    return {
      currentIndex,
      carouselInnerDom,
      stepStyle,
      carouselStyle,
      currentChildren,
      initCarousel,
      pathClick,
      mouseenter,
      dotsClick
    }
  }
})
</script>

<style scoped lang="scss">
.fx-carousel {
  width: 100%;
  position: relative;
  overflow: hidden;

  &:hover {
    > .fx-carousel-path.hover {
      transform: translateX(0) !important;
      opacity: 1 !important;
    }

    > .fx-carousel-dots.hover {
      transform: translateX(-50%) !important;
    }
  }

  > .fx-carousel-inner {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
  }

  > .fx-carousel-path {
    cursor: pointer;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 10px;
    top: 50%;
    transform: translateY(-50%);
    background-color: rgba(0, 0, 0, .3);
    color: #fff;
    border-radius: 50%;
    transition: all 0.15s;

    &:hover {
      background-color: rgba(0, 0, 0, .7);
    }

    &.right {
      left: auto;
      right: 10px;
    }

    &.hover {
      transform: translateX(calc(-100% - 10px));
      opacity: 0;

      &.right {
        transform: translateX(calc(100% + 10px));
      }
    }
  }

  > .fx-carousel-dots {
    position: absolute;
    left: 50%;
    bottom: 20px;
    transform: translateX(-50%);
    transition: transform 0.15s;
    display: flex;

    &.hover {
      transform: translateX(-50%) translateY(calc(100% + 20px));
    }

    > li {
      cursor: pointer;
      height: 2px;
      width: 20px;
      margin: 0 5px;
      background-color: rgba(255, 255, 255, .5);
      position: relative;

      > .step {
        position: absolute;
        left: 0;
        top: 0;
        width: 0;
        height: 100%;
        background-color: #fff;
      }

      &.active > .step {
        width: 20px;
      }
    }
  }
}
</style>
