<template>
  <div class="fx-switch" :class="[modelValue?'open':'close',{disabled}]" @click="switchClick">
    <div class="out-left-text" v-if="$slots.close||closeText!==''" :style="textStyle('close')">
      <slot name="close">{{ closeText }}</slot>
    </div>
    <div class="switch-wrapper" :style="switchStyle">
      <div class="dot" :class="{open:modelValue}"></div>
    </div>
    <div class="out-right-text" v-if="$slots.open||openText!==''" :style="textStyle('open')">
      <slot name="open">{{ openText }}</slot>
    </div>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, PropType, ref } from 'vue'
import { useModelWatch } from '@/base/hooks'
import { colorRgb } from '@/base/utils'

interface SwitchProps {
  modelValue: boolean,
  closeText: string,
  openText: string,
  closeColor: string,
  openColor: string,
  disabled: boolean
}

export default defineComponent({
  name: 'FSwitch',
  props: {
    modelValue: {
      type: Boolean as PropType<boolean>,
      default: false,
      required: true
    },
    // 关闭文字
    closeText: {
      type: String as PropType<string>,
      default: ''
    },
    // 开启文字
    openText: {
      type: String as PropType<string>,
      default: ''
    },
    // 关闭颜色
    closeColor: {
      type: String as PropType<string>,
      default: 'rgb(219,213,213)'
    },
    // 开启颜色
    openColor: {
      type: String as PropType<string>,
      default: 'rgb(45, 183, 245)'
    },
    // 禁用
    disabled: {
      type: Boolean as PropType<boolean>,
      default: false
    }
  },
  emits: ['update:modelValue', 'change'],
  setup (props: SwitchProps) {
    // 当前状态
    const switchValue = ref<boolean>(props.modelValue)
    // 数据双向绑定hook
    useModelWatch<boolean>(() => props.modelValue, switchValue)

    // 样式处理
    const switchStyle = computed<{ backgroundColor: string }>(() => {
      const closeColor = props.disabled ? colorRgb(props.closeColor, 0.3) : props.closeColor
      const openColor = props.disabled ? colorRgb(props.openColor, 0.3) : props.openColor
      return {
        backgroundColor: switchValue.value ? openColor : closeColor
      }
    })

    // 点击事件
    const switchClick = (): void => {
      if (props.disabled) return
      switchValue.value = !switchValue.value
    }

    // 文字颜色
    const textStyle = (status: 'open' | 'close'): { color: string | null } => {
      const closeColor = props.disabled ? colorRgb(props.closeColor, 0.3) : props.closeColor
      const openColor = props.disabled ? colorRgb(props.openColor, 0.3) : props.openColor
      if (status === 'open' && switchValue.value) return { color: openColor }
      if (status === 'close' && !switchValue.value) return { color: closeColor }
      return { color: props.disabled ? '#C0C4CC' : null }
    }

    return {
      switchValue,
      switchStyle,
      switchClick,
      textStyle
    }
  }
})
</script>

<style scoped lang="scss">
@import "../scss/base";

.fx-switch {
  display: inline-flex;
  cursor: pointer;
  height: 20px;
  line-height: 20px;

  &.disabled {
    cursor: not-allowed;
  }

  > .out-left-text {
    margin-right: 10px;
  }

  > .out-right-text {
    margin-left: 10px;
  }

  > .switch-wrapper {
    width: 40px;
    height: 20px;
    border-radius: 10px;
    position: relative;
    transition: all 0.15s;

    > .dot {
      width: 16px;
      height: 16px;
      border-radius: 8px;
      background-color: #fff;
      position: absolute;
      top: 2px;
      left: 2px;
      transition: all 0.15s;

      &.open {
        transform: translateX(calc(40px - 16px - 4px));
      }
    }
  }
}
</style>
