<template>
  <button class="fx-button" v-if="type!=='text'"
          :class="[type,size,{plain,round,circle,disabled:disabled||loading,hover:!disabled&&!loading}]"
          @click="handleButtonClick">
    <span class="loading animation" v-if="loading">
      <i class="iconfont icon-loading fs14"/>
    </span>
    <span class="loading" v-if="icon!==''">
      <i class="fs14 iconfont" :class="icon"/>
    </span>
    <slot></slot>
  </button>
  <a v-else v-bind="$attrs" class="fx-button-text" :class="{disabled:disabled||loading,hover:!disabled&&!loading}">
    <slot></slot>
  </a>
</template>

<script lang="ts">
import { defineComponent, PropType, SetupContext } from 'vue'
import { ComponentSize, componentSizeArr, ComponentTypes, componentTypesArr } from '@/base/types'

export declare interface ButtonProps {
  type: ComponentTypes | 'text',
  size: ComponentSize,
  plain: boolean,
  round: boolean,
  circle: boolean,
  disabled: boolean,
  loading: boolean,
  icon: string
}

export default defineComponent({
  name: 'FButton',
  emits: ['click'],
  inheritAttrs: true,
  props: {
    // 样式类型
    type: {
      type: String as PropType<ComponentTypes | 'text'>,
      default: '',
      validator (value: string): boolean {
        return [...componentTypesArr, 'text'].includes(value)
      }
    },
    // 大小类型
    size: {
      type: String as PropType<ComponentSize>,
      default: 'default',
      validator (value: string): boolean {
        return componentSizeArr.includes(value)
      }
    },
    // 镂空
    plain: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 圆角
    round: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 圆角
    circle: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 禁用
    disabled: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 加载
    loading: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 图标
    icon: {
      type: String as PropType<string>,
      default: ''
    }
  },
  setup (props: ButtonProps, ctx: SetupContext) {
    // 点击事件
    const handleButtonClick = (ev: MouseEvent): void => {
      if (props.disabled || props.loading) return
      ctx.emit('click', ev)
    }

    return {
      handleButtonClick
    }
  }
})
</script>

<style scoped lang="scss">
@import "../scss/base";

@keyframes button-loading-animation {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}

.fx-button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background-color: $button-color-bg;
  height: $height-default;
  line-height: $height-default;
  padding: $button-padding-default;
  border: 1px solid $border-color-default;
  cursor: pointer;
  transition: all 0.1s;
  outline: none;
  white-space: nowrap;
  border-radius: $border-radius-default;
  color: $font-color-default;
  user-select: none;

  &.circle {
    padding: 0 !important;
    text-align: center;
    display: inline-flex;
    justify-content: center;
    align-items: center;

    > .loading {
      margin-right: 0;
    }
  }

  &.disabled {
    cursor: not-allowed !important;
    color: $font-color-lowest;
  }

  &.hover:hover {
    border-color: $button-color-primary-bg-7;
    background-color: $button-color-primary-bg-1;
    color: $button-color-primary;
  }

  &.large {
    height: $height-large;
    line-height: $height-large;
    padding: $button-padding-large;

    &.round {
      border-radius: calc(#{$height-large} / 2);
    }

    &.circle {
      border-radius: calc(#{$height-large} / 2);
      width: $height-large;
      overflow: hidden;
    }
  }

  &.middle {
    height: $height-middle;
    line-height: $height-middle;
    padding: $button-padding-middle;

    &.round {
      border-radius: calc(#{$height-middle} / 2);
    }

    &.circle {
      border-radius: calc(#{$height-middle} / 2);
      width: $height-middle;
      overflow: hidden;
    }
  }

  &.default {
    height: $height-default;
    line-height: $height-default;
    padding: $button-padding-default;

    &.round {
      border-radius: calc(#{$height-default} / 2);
    }

    &.circle {
      border-radius: calc(#{$height-default} / 2);
      width: $height-default;
      overflow: hidden;
    }
  }

  &.mini {
    height: $height-mini;
    line-height: $height-mini;
    padding: $button-padding-mini;

    &.round {
      border-radius: calc(#{$height-mini} / 2);
    }

    &.circle {
      border-radius: calc(#{$height-mini} / 2);
      width: $height-mini;
      overflow: hidden;
    }
  }

  &.primary {
    background-color: $button-color-primary;
    border-color: $button-color-primary;
    color: #fff;

    &.hover:hover {
      background-color: $button-color-primary-bg-7;
      border-color: $button-color-primary-bg-7;
      color: #fff;
    }

    &.disabled {
      background-color: $button-color-primary-bg-3;
      border-color: $button-color-primary-bg-3;
    }

    &.plain {
      background-color: $button-color-primary-bg-1;
      color: $button-color-primary;
      border-color: $button-color-primary;

      &.disabled {
        color: $button-color-primary-bg-5;
        border-color: $button-color-primary-bg-5;
      }

      &.hover:hover {
        background-color: $button-color-primary;
        color: #fff;
      }
    }
  }

  &.success {
    background-color: $button-color-success;
    border-color: $button-color-success;
    color: #fff;

    &.hover:hover {
      background-color: $button-color-success-bg-7;
      border-color: $button-color-success-bg-7;
      color: #fff;
    }

    &.disabled {
      background-color: $button-color-success-bg-3;
      border-color: $button-color-success-bg-3;
    }

    &.plain {
      background-color: $button-color-success-bg-1;
      color: $button-color-success;
      border-color: $button-color-success;

      &.disabled {
        color: $button-color-success-bg-5;
        border-color: $button-color-success-bg-5;
      }

      &.hover:hover {
        background-color: $button-color-success;
        color: #fff;
      }
    }
  }

  &.warning {
    background-color: $button-color-warning;
    border-color: $button-color-warning;
    color: #fff;

    &.hover:hover {
      background-color: $button-color-warning-bg-7;
      border-color: $button-color-warning-bg-7;
      color: #fff;
    }

    &.disabled {
      background-color: $button-color-warning-bg-3;
      border-color: $button-color-warning-bg-3;
    }

    &.plain {
      background-color: $button-color-warning-bg-1;
      color: $button-color-warning;
      border-color: $button-color-warning;

      &.disabled {
        color: $button-color-warning-bg-5;
        border-color: $button-color-warning-bg-5;
      }

      &.hover:hover {
        background-color: $button-color-warning;
        color: #fff;
      }
    }
  }

  &.error {
    background-color: $button-color-error;
    border-color: $button-color-error;
    color: #fff;

    &.hover:hover {
      background-color: $button-color-error-bg-7;
      border-color: $button-color-error-bg-7;
      color: #fff;
    }

    &.disabled {
      background-color: $button-color-error-bg-3;
      border-color: $button-color-error-bg-3;
    }

    &.plain {
      background-color: $button-color-error-bg-1;
      color: $button-color-error;
      border-color: $button-color-error;

      &.disabled {
        color: $button-color-error-bg-5;
        border-color: $button-color-error-bg-5;
      }

      &.hover:hover {
        background-color: $button-color-error;
        color: #fff;
      }
    }
  }

  > .loading {
    display: inline-block;
    margin-right: 5px;
    width: 16px;
    height: 16px;
    line-height: 16px;
    transform-origin: center center;

    &.animation {
      animation: button-loading-animation 0.5s infinite linear;
    }
  }
}

.fx-button-text {
  user-select: none;
  cursor: pointer;
  color: $button-color-primary;

  &.hover:hover {
    color: $button-color-primary-bg-7;
  }

  &.disabled {
    color: $button-color-disabled;
  }
}
</style>
