<template>
  <div class="fx-slider" :class="[size,{disabled}]">
    <div class="slider-line" :class="{'click-animation':!dragStart,'input-number':showInput}"
         ref="sliderLineDom" @click="sliderLineClick">
      <div class="slider-inline" :style="{width:`${sliderValue}%`}"></div>
      <div class="slider-dot" ref="sliderDotDom" :style="{left:`${sliderValue}%`}"></div>
      <div class="tooltip" :class="{active:dragStart}" v-if="showTooltip" :style="{left:`${sliderValue}%`}">
        {{ tooltipValue }}
      </div>
      <ul class="step-dot" v-if="showStep&&step">
        <li v-for="(item,index) in Math.floor(100/step)" :key="item" :style="{left:`${100/(100/step)*index}%`}"></li>
      </ul>
    </div>
    <fx-input-number v-if="showInput" v-model="sliderValue" :size="size" width="120" :min="0" :max="100"/>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, onMounted, onUnmounted, PropType, reactive, ref } from 'vue'
import { useModelWatch } from '@/base/hooks'
import { ComponentSize, componentSizeArr } from '@/base/types'
import inputNumber from '../input/inputNumber.vue'

interface SliderProps {
  modelValue: number,
  size: ComponentSize,
  disabled: boolean,
  showTooltip: boolean,
  format: (val: number) => string,
  step: number,
  showStep: boolean,
  showInput: boolean
}

export default defineComponent({
  name: 'FSlider',
  components: {
    fxInputNumber: inputNumber
  },
  props: {
    // v-model
    modelValue: {
      type: Number as PropType<number>,
      default: 0,
      required: true
    },
    // 大小
    size: {
      type: String as PropType<ComponentSize>,
      default: 'default',
      validator (val: string): boolean {
        return componentSizeArr.includes(val)
      }
    },
    // 禁用
    disabled: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 显示提示
    showTooltip: {
      type: Boolean as PropType<boolean>,
      default: true
    },
    // 格式化
    format: {
      type: Function as PropType<() => string>,
      default: null
    },
    // 步数
    step: {
      type: Number as PropType<number>,
      default: 0
    },
    // 显示步数
    showStep: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 显示输入框
    showInput: {
      type: Boolean as PropType<boolean>,
      default: false
    }
  },
  setup (props: SliderProps) {
    // 当前滑块值
    const sliderValue = ref<number>(props.modelValue)
    useModelWatch<number>(() => props.modelValue, sliderValue)
    // 滑块线DOM
    const sliderLineDom = ref<HTMLElement | undefined>(undefined)
    // 滑块线DOM
    const sliderDotDom = ref<HTMLElement | undefined>(undefined)
    // 提示显示
    const tooltipValue = computed(() => {
      if (!props.showTooltip) return ''
      const value = Math.floor(sliderValue.value)
      if (props.format && typeof props.format === 'function') {
        return props.format(value)
      }
      return value
    })
    const disStart = reactive<{ left: number, oldLeft: number }>({
      left: 0,
      oldLeft: 0
    })
    const dragStart = ref<boolean>(false)

    // 点击滑块
    const sliderLineClick = (ev: MouseEvent): void => {
      if (!sliderLineDom.value || props.disabled) return
      const rect = sliderLineDom.value.getBoundingClientRect()
      const dis = Math.floor((ev.x - rect.x) / rect.width * 100)
      if (props.step) {
        if (((dis % props.step) + 1) >= (props.step / 2)) {
          sliderValue.value = Math.floor(dis / props.step) * props.step + props.step
        } else {
          sliderValue.value = Math.floor(dis / props.step) * props.step
        }
      } else {
        sliderValue.value = Math.floor(dis)
      }
    }
    const handleMousedown = (ev: MouseEvent): void => {
      ev.stopPropagation()
      ev.preventDefault()
      if (props.disabled) return
      dragStart.value = true
      disStart.left = ev.clientX
      disStart.oldLeft = sliderValue.value
      document.addEventListener('mousemove', handleMousemove)
      document.addEventListener('mouseup', handleMouseup)
    }
    const handleMousemove = (ev: MouseEvent): void => {
      ev.stopPropagation()
      ev.preventDefault()
      if (!sliderDotDom.value || !sliderLineDom.value || !dragStart.value) return
      let disLeft = (ev.clientX - disStart.left) / sliderLineDom.value.clientWidth * 100 + disStart.oldLeft
      if (disLeft <= 0) disLeft = 0
      if (disLeft >= 100) disLeft = 100
      if (props.step) {
        if (((disLeft % props.step) + 1) >= (props.step / 2)) {
          sliderValue.value = Math.floor(disLeft / props.step) * props.step + props.step
        } else {
          sliderValue.value = Math.floor(disLeft / props.step) * props.step
        }
      } else {
        sliderValue.value = Math.floor(disLeft)
      }
    }
    const handleMouseup = (ev: MouseEvent): void => {
      ev.stopPropagation()
      ev.preventDefault()
      dragStart.value = false
      document.removeEventListener('mousemove', handleMousemove)
      document.removeEventListener('mouseleave', handleMouseup)
    }

    onMounted(() => {
      if (!sliderDotDom.value) return
      sliderDotDom.value.addEventListener('mousedown', handleMousedown)
    })
    onUnmounted(() => {
      if (!sliderDotDom.value) return
      sliderDotDom.value.removeEventListener('mousedown', handleMousedown)
    })

    return {
      dragStart,
      sliderValue,
      tooltipValue,
      sliderDotDom,
      sliderLineDom,
      sliderLineClick
    }
  }
})
</script>

<style scoped lang="scss">
@import "../scss/base";

.fx-slider {
  user-select: none;
  position: relative;
  display: flex;
  align-items: center;

  &.large {
    height: $slider-large-height;
    line-height: $slider-large-height;

    > .slider-line {
      height: $slider-large-line;
      border-radius: calc(#{$slider-large-line} / 2);

      > .slider-inline {
        border-radius: calc(#{$slider-large-line} / 2);
      }

      > .slider-dot {
        top: calc(-10px + #{$slider-large-line} / 2);
      }

      > .step-dot {
        height: $slider-large-line;

        > li {
          width: $slider-large-line;
          height: $slider-large-line;
        }
      }
    }
  }

  &.middle {
    height: $slider-middle-height;
    line-height: $slider-middle-height;

    > .slider-line {
      height: $slider-middle-line;
      border-radius: calc(#{$slider-middle-line} / 2);

      > .slider-inline {
        border-radius: calc(#{$slider-middle-line} / 2);
      }

      > .slider-dot {
        top: calc(-10px + #{$slider-middle-line} / 2);
      }

      > .step-dot {
        height: $slider-middle-line;

        > li {
          width: $slider-middle-line;
          height: $slider-middle-line;
        }
      }
    }
  }

  &.default {
    height: $slider-default-height;
    line-height: $slider-default-height;

    > .slider-line {
      height: $slider-default-line;
      border-radius: calc(#{$slider-default-line} / 2);

      > .slider-inline {
        border-radius: calc(#{$slider-default-line} / 2);
      }

      > .slider-dot {
        top: calc(-10px + #{$slider-default-line} / 2);
      }

      > .step-dot {
        height: $slider-default-line;

        > li {
          width: $slider-default-line;
          height: $slider-default-line;
        }
      }
    }
  }

  &.mini {
    height: $slider-mini-height;
    line-height: $slider-mini-height;

    > .slider-line {
      height: $slider-mini-line;
      border-radius: calc(#{$slider-mini-line} / 2);

      > .slider-inline {
        border-radius: calc(#{$slider-mini-line} / 2);
      }

      > .slider-dot {
        top: calc(-10px + #{$slider-mini-line} / 2);
      }

      > .step-dot {
        height: $slider-mini-line;

        > li {
          width: $slider-mini-line;
          height: $slider-mini-line;
        }
      }
    }
  }

  &.disabled {
    > .slider-line {
      cursor: not-allowed;

      > .slider-inline {
        background-color: $slider-line-color-disabled;
      }

      > .slider-dot {
        border-color: $slider-line-color-disabled;
      }
    }
  }

  > .slider-line {
    width: 100%;
    background-color: $slider-line-color;
    cursor: pointer;
    position: relative;

    &.click-animation {
      > .slider-inline, > .slider-dot {
        transition: all 0.15s;
      }
    }

    &.input-number {
      margin-right: 20px;
    }

    > .slider-inline {
      height: 100%;
      background-color: $color-primary;
      position: absolute;
      z-index: 5;
      left: 0;
      top: 0;
    }

    > .slider-dot {
      width: 20px;
      height: 20px;
      box-sizing: border-box;
      border-radius: 10px;
      background-color: #fff;
      border: 2px solid $color-primary;
      position: absolute;
      z-index: 10;
      top: -50%;
      transform: translateX(-50%);
      transform-origin: center center;

      &:hover {
        transform: translateX(-50%) scale(1.2);

        + .tooltip {
          display: block;
        }
      }
    }

    > .tooltip {
      display: none;
      position: absolute;
      z-index: 20;
      left: 50%;
      top: 0;
      line-height: 1;
      padding: 5px 15px;
      background-color: #303133;
      color: #fff;
      border-radius: $border-radius-default;
      transform: translateX(-50%) translateY(calc(-100% - 15px));

      &.active {
        display: block;
      }

      &::after {
        content: '';
        position: absolute;
        border: 6px solid transparent;
        left: 50%;
        bottom: -12px;
        transform: translateX(-50%);
        border-top-color: #303133;
      }
    }

    > .step-dot {
      width: 100%;
      height: 100%;
      position: absolute;

      > li {
        position: absolute;
        top: 0;
        background-color: #fff;
        border-radius: 50%;
      }
    }
  }
}
</style>
