<template>
  <div class="fx-pagination" :class="[size,{border}]">
    <div class="total mr5">共 <em>{{ total }}</em> 条</div>
    <div @click="previous" class="previous" :class="{disabled:currentPage<=1}">
      <slot name="previous"><i class="iconfont icon-left fs10"/></slot>
    </div>
    <ul class="fx-pages">
      <li v-if="previousPageShow" @click="pageTo(currentPage-5<=1?1:currentPage-5)">
        <i class="iconfont icon-more"></i>
      </li>
      <li v-for="item in pagesArr" :key="item" @click="pageTo(item)" :class="{active:currentPage===item}">
        {{ item }}
      </li>
      <li v-if="nextPageShow" @click="pageTo(currentPage+5>=pageTotal?pageTotal:currentPage+5)">
        <i class="iconfont icon-more"></i>
      </li>
    </ul>
    <div @click="next" class="next" :class="{disabled:currentPage>=pageTotal}">
      <slot name="next"><i class="iconfont icon-right fs10"/></slot>
    </div>
    <div class="page-size" v-if="pageSize instanceof Array&&pageSize.length">
      <fx-select v-model="currentPageSize" :size="size" width="70" @change="currentPage=1">
        <fx-option v-for="item in pageSize" :key="item" :label="item" :value="item"/>
      </fx-select>
    </div>
    <div class="page-to ml5">
      <span>前往</span>
      <fx-input-number width="50" v-model="currentPage" :size="size"
                       :is-btn="false" :min="1" :max="pageTotal" style="margin-right: 0;"/>
      <span>页</span>
    </div>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, onMounted, PropType, ref } from 'vue'
import { useModelWatch } from '@/base/hooks'
import { ComponentSize, componentSizeArr } from '@/base/types'

interface PaginationProps {
  modelValue: number;
  total: number;
  pageCount: number;
  size: ComponentSize;
  border: boolean;
  pageSize: number | number[];
}

export default defineComponent({
  name: 'pagination',
  props: {
    modelValue: {
      type: Number as PropType<number>,
      default: 1
    },
    // 总数
    total: {
      type: Number as PropType<number>,
      default: 10
    },
    // 分页个数
    pageCount: {
      type: Number as PropType<number>,
      default: 5
    },
    // 大小
    size: {
      type: String as PropType<ComponentSize>,
      default: 'default',
      validator (val: string): boolean {
        return componentSizeArr.includes(val)
      }
    },
    border: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 分页大小
    pageSize: {
      type: [Number, Array] as PropType<number | number[]>,
      default: 10
    }
  },
  setup (props: PaginationProps) {
    // 当前页
    const currentPage = ref<number>(props.modelValue)
    useModelWatch(() => props.modelValue, currentPage)
    // 当前分页大小
    const currentPageSize = ref<number>(10)
    // 分页页数总量
    const pageTotal = computed<number>(() => Math.ceil(props.total / currentPageSize.value))
    // 显示more 上一页
    const previousPageShow = computed<boolean>(() => {
      const count: number = props.pageCount % 2 === 1 ? props.pageCount : props.pageCount + 1
      const startPage: number = currentPage.value - (count - 1) / 2
      return startPage > 1 && pageTotal.value > pagesArr.value.length
    })
    // 显示more 下一页
    const nextPageShow = computed<boolean>(() => {
      const count: number = props.pageCount % 2 === 1 ? props.pageCount : props.pageCount + 1
      const endPage: number = currentPage.value + (count - 1) / 2
      return endPage < pageTotal.value && pageTotal.value > pagesArr.value.length
    })
    // 分页中间间隔
    const pagesArr = computed<number[]>(() => {
      const array: number[] = []
      let count: number = props.pageCount % 2 === 1 ? props.pageCount : props.pageCount + 1
      if (pageTotal.value < count) count = pageTotal.value
      let startPage: number = currentPage.value - (count - 1) / 2
      let endPage: number = currentPage.value + (count - 1) / 2
      if (startPage < 1) {
        endPage = endPage + (1 - startPage)
        startPage = 1
      }
      if (endPage > pageTotal.value) {
        startPage = startPage - (endPage - pageTotal.value)
        endPage = pageTotal.value
      }
      for (let i = startPage; i <= endPage; i++) {
        array.push(i)
      }
      return array
    })

    const pageTo = (page: number): void => {
      currentPage.value = page
    }
    const previous = (): void => {
      currentPage.value--
      if (currentPage.value <= 1) currentPage.value = 1
    }
    const next = (): void => {
      currentPage.value++
      if (currentPage.value >= pageTotal.value) currentPage.value = pageTotal.value
    }

    onMounted(() => {
      if (props.pageSize instanceof Array && props.pageSize.length) {
        currentPageSize.value = props.pageSize[0]
      } else if (!(props.pageSize instanceof Array)) {
        currentPageSize.value = props.pageSize
      }
    })

    return {
      pagesArr,
      currentPage,
      previousPageShow,
      nextPageShow,
      currentPageSize,
      pageTotal,
      pageTo,
      previous,
      next
    }
  }
})
</script>

<style scoped lang="scss">
@import "../scss/base";

.fx-pagination {
  user-select: none;
  display: flex;
  align-items: center;
  margin-bottom: 10px;

  &.large {
    height: $height-large;

    > .fx-pages {
      > li {
        min-width: $height-large;
        line-height: calc(#{$height-large} - 2px);
      }
    }

    > .previous, > .next {
      min-width: $height-large;
      line-height: calc(#{$height-large} - 2px);
    }
  }

  &.middle {
    height: $height-middle;

    > .fx-pages {
      > li {
        min-width: $height-middle;
        line-height: calc(#{$height-middle} - 2px);
      }
    }

    > .previous, > .next {
      min-width: $height-middle;
      line-height: calc(#{$height-middle} - 2px);
    }
  }

  &.default {
    height: $height-default;

    > .fx-pages {
      > li {
        min-width: $height-default;
        line-height: calc(#{$height-default} - 2px);
      }
    }

    > .previous, > .next {
      min-width: $height-default;
      line-height: calc(#{$height-default} - 2px);
    }
  }

  &.mini {
    height: $height-mini;

    > .fx-pages {
      > li {
        min-width: $height-mini;
        line-height: calc(#{$height-mini} - 2px);
      }
    }

    > .previous, > .next {
      min-width: $height-mini;
      line-height: calc(#{$height-mini} - 2px);
    }
  }

  &.border {
    > .previous, > .next {
      border-radius: $border-color-default;
      border: 1px solid $border-color-important;
      background-color: #fff;
    }

    > .fx-pages > li {
      border-radius: $border-color-default;
      border-color: $border-color-important;
      background-color: #fff;

      &.active {
        background-color: $color-primary;
        color: #fff;
      }
    }
  }

  > .previous, > .next {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    height: 100%;
    box-sizing: border-box;
    margin-right: 5px;
    padding: 0 10px;

    &.disabled {
      cursor: not-allowed;
      color: $font-color-lowest;
    }
  }

  > .fx-pages {
    display: flex;
    height: 100%;

    > li {
      cursor: pointer;
      height: 100%;
      box-sizing: border-box;
      margin-right: 5px;
      border: 1px solid transparent;
      text-align: center;
      padding: 0 5px;

      &.active {
        color: $color-primary;
      }
    }
  }

  > .total, > .page-to {
    display: flex;
    align-items: center;
  }
}
</style>
