
import { defineComponent, PropType, ref } from 'vue'
import { ComponentTheme } from '@/base/types'

export type TooltipPosition =
  'topLeft'
  | 'top'
  | 'topRight'
  | 'rightTop'
  | 'right'
  | 'rightBottom'
  | 'bottomLeft'
  | 'bottom'
  | 'bottomRight'
  | 'leftTop'
  | 'left'
  | 'leftBottom'

export interface TooltipProps {
  title: string | number;
  position: TooltipPosition;
  width: string | number;
  theme: ComponentTheme
}

export const TooltipPositionArr = ['topLeft', 'top', 'topRight', 'rightTop', 'right', 'rightBottom', 'bottomLeft', 'bottom', 'bottomRight', 'leftTop', 'left', 'leftBottom']

export default defineComponent({
  name: 'tooltip',
  props: {
    // 提示文字
    title: {
      type: [String, Number] as PropType<string | number>,
      default: '',
      required: true
    },
    // 提示方向
    position: {
      type: String as PropType<TooltipPosition>,
      default: 'top',
      validator (val: string): boolean {
        return TooltipPositionArr.includes(val)
      }
    },
    width: {
      type: [String, Number] as PropType<string | number>,
      default: 220
    },
    theme: {
      type: String as PropType<ComponentTheme>,
      default: 'dark'
    }
  },
  setup (props: TooltipProps) {
    const titleValue = ref<string | number>(props.title)

    const setTitleValue = (val: string | number): void => {
      titleValue.value = val
    }

    return {
      titleValue,
      setTitleValue
    }
  }
})
