<template>
  <div class="fx-card" :class="{'is-padding':isPadding}" :style="cardStyle">
    <div class="title fs16" v-if="title!==''||$slots.title">
      <slot name="title">{{ title }}</slot>
    </div>
    <div class="content" :style="contentStyle">
      <slot name="default"></slot>
    </div>
    <div class="hide-wrapper" v-if="$slots.hide" ref="hideWrapperDom">
      <div class="hide-content" v-show="hideShow">
        <slot name="hide"></slot>
      </div>
      <div class="hide-bar" @click="hideClick">
        <div class="center">
          <i class="fs12 mr5 iconfont icon-to-bottom" :class="{close:hideShow}"/>
          <span>{{ hideShow ? '隐藏' : '显示' }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType, ref } from 'vue'

// interface CardProps {
//   title: string;
//   isPadding: boolean;
//   contentStyle: CSSStyleSheet;
//   cardStyle: CSSStyleSheet;
// }

export default defineComponent({
  name: 'card',
  props: {
    title: {
      type: String as PropType<string>,
      default: ''
    },
    isPadding: {
      type: Boolean as PropType<boolean>,
      default: true
    },
    contentStyle: {
      type: Object as PropType<CSSStyleSheet>,
      default: null
    },
    cardStyle: {
      type: Object as PropType<CSSStyleSheet>,
      default: null
    }
  },
  setup () {
    // 显示 / 隐藏 底部数据
    const hideShow = ref<boolean>(false)
    const hideWrapperDom = ref<HTMLElement>()
    const contentHeight = ref<number>(0)

    const initHide = () => {
      if (!hideWrapperDom.value) return
      const height = hideWrapperDom.value?.offsetHeight
      if (!contentHeight.value) contentHeight.value = height - 30
      hideWrapperDom.value.style.height = hideShow.value ? `${contentHeight.value || height - 30}px` : '0'
    }

    const hideClick = (): void => {
      hideShow.value = !hideShow.value
      setTimeout(initHide)
    }

    return {
      hideShow,
      hideWrapperDom,
      hideClick
    }
  }
})
</script>

<style scoped lang="scss">
@import "../scss/base";

.fx-card {
  box-shadow: $border-shadow;
  border-radius: $border-radius-default;
  margin-bottom: 20px;
  background-color: #fff;
  overflow: hidden;

  &.is-padding {
    > .title {
      padding: 10px 20px;
    }

    > .content {
      padding: 20px;
    }

    > .hide-wrapper > .hide-content {
      padding: 20px;
    }
  }

  &:hover {
    > .hide-wrapper > .hide-bar > .center {
      left: calc(50% - 45px);

      > span {
        opacity: 1;
      }
    }
  }

  > .title {
    height: 20px;
    line-height: 20px;
    border-bottom: 1px solid #ebeef5;
  }

  > .hide-wrapper {
    width: 100%;
    transition: all 0.2s;
    position: relative;
    padding-bottom: 30px;

    > .hide-bar {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      cursor: pointer;
      user-select: none;
      text-align: center;
      height: 30px;
      line-height: 30px;
      border-top: 1px solid $border-color-default;
      transition: all 0.1s;
      color: $font-color-lowest;

      &:hover {
        color: $color-primary;
      }

      > .center {
        display: flex;
        position: absolute;
        left: calc(50% - 12px);
        transition: all 0.1s;

        > .close {
          display: inline-block;
          transform-origin: center center;
          transform: rotate(180deg);
          transition: all 0.1s;
        }

        > span {
          opacity: 0;
          transition: all 0.1s;
        }
      }
    }

    > .hide-content {
      border-top: 1px solid $border-color-default;
    }
  }
}
</style>
