<template>
  <div class="fx-checkbox"
       :class="{active:handleActive(checkboxGroupValue),disabled:handleDisabled(checkboxGroupValue),}"
       @click="checkboxClick(checkboxGroupValue)">
    <div class="fx-checkbox-select-box">
      <input type="checkbox" hidden :name="isChecked">
      <i class="icon iconfont icon-check fs10"/>
    </div>
    <div class="fx-checkbox-label">
      <slot>{{ label }}</slot>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, inject, onMounted, PropType, ref } from 'vue'
import { Emitter } from 'mitt'
import { useModelWatch } from '@/base/hooks'

interface CheckboxProps {
  modelValue: boolean,
  disabled: boolean,
  label: string
}

export default defineComponent({
  name: 'FCheckbox',
  props: {
    // v-model
    modelValue: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 禁用
    disabled: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 选项
    label: {
      type: String as PropType<string>,
      default: ''
    }
  },
  emits: ['update:modelValue', 'change'],
  setup (props: CheckboxProps) {
    // 是否选中
    const isChecked = ref<boolean>(props.modelValue)
    // 获取当前 checkboxGroup 选中数据集
    const checkboxGroupValue = inject<string[]>('checkboxGroupValue', [])
    // 获取当前 checkboxGroup 禁用
    const checkboxGroupDisabled = inject<boolean>('checkboxGroupDisabled', false)
    // 获取当前 checkboxGroup 选中个数限制
    type CheckboxNumberProps = { max: number, min: number } | undefined
    const checkboxGroupNumber = inject<CheckboxNumberProps>('checkboxGroupNumber', undefined)
    // 获取checkboxGroup 传数据
    const checkboxGroupMittEmit = inject<Emitter['emit'] | undefined>('checkboxGroupMittEmit', undefined)
    // 接收formItem mitt 监听器
    const formItemMitt = inject<Emitter['emit'] | undefined>('formItemMitt', undefined)

    // 处理选中样式
    const handleActive = (arr: string[]): boolean => {
      // 当不存在父组件时 直接使用自身选中状态
      if (!checkboxGroupMittEmit) return isChecked.value
      // 根据选中的数组 显示选中状态
      return arr.some((item: string) => item === props.label)
    }
    // 处理禁用样式
    const handleDisabled = (arr: string[]): boolean => {
      // 禁用状态
      let isMaxMin = false
      // 如果存在父组件的列表数据  并且存在选中个数限制
      if (arr && arr.length && checkboxGroupNumber) {
        isMaxMin = arr.length >= checkboxGroupNumber.max || arr.length < checkboxGroupNumber.min
        // 将非选中状态显示可点状态
        isMaxMin = isMaxMin && !handleActive(arr)
      }
      return checkboxGroupDisabled || props.disabled || isMaxMin
    }
    // 多选框点击
    const checkboxClick = (arr: string[]): void => {
      if (props.disabled || checkboxGroupDisabled || handleDisabled(arr)) return
      if (checkboxGroupMittEmit && checkboxGroupValue) {
        checkboxGroupMittEmit('checkboxMitt', props.label)
      } else {
        isChecked.value = !isChecked.value
        if (formItemMitt) formItemMitt('formItemValue', isChecked.value)
      }
    }
    // 数据双向绑定hook
    useModelWatch<boolean>(() => props.modelValue, isChecked)

    onMounted(() => {
      if (formItemMitt) formItemMitt('formItemValue', isChecked.value)
    })

    return {
      isChecked,
      checkboxGroupValue,
      checkboxGroupDisabled,
      handleActive,
      handleDisabled,
      checkboxClick
    }
  }
})
</script>

<style scoped lang="scss">
@import "../scss/base";

.fx-checkbox {
  user-select: none;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: $font-color-default;
  transition: all 0.15s ease-in;
  margin-right: 10px;

  &.active {
    color: $color-primary;

    > .fx-checkbox-select-box {
      background-color: $color-primary;
      border-color: $color-primary;
      color: #fff;

      > i.icon {
        transform: scale(1);
      }
    }
  }

  &.disabled {
    color: $font-color-lowest;
    cursor: not-allowed;

    &.active {
      > .fx-checkbox-select-box {
        color: $font-color-lowest !important;
      }
    }

    > .fx-checkbox-select-box {
      background-color: #f2f6fc;
      border-color: #dcdfe6;
    }
  }

  > .fx-checkbox-select-box {
    background-color: #fff;
    color: #fff;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    position: relative;
    border: 1px solid $border-color-default;
    border-radius: $border-radius;
    box-sizing: border-box;
    width: 14px;
    height: 14px;
    line-height: 14px;
    transition: border-color .25s cubic-bezier(.71, -.46, .29, 1.46), background-color .25s cubic-bezier(.71, -.46, .29, 1.46);

    > i.icon {
      display: inline-block;
      transform-origin: center center;
      transform: scale(0);
      transition: transform .15s ease-in .05s;
    }
  }

  > .fx-checkbox-label {
    padding-left: 5px;
  }
}
</style>
