<template>
  <div class="fx-input-number" :class="{disabled,right}">
    <div class="less" v-if="isBtn" :class="[size,{disabled:min&&inputNumberValue<=min}]" @click="lessClick">
      <i class="iconfont icon-Less fs16"/>
    </div>
    <input type="number" v-model="inputNumberValue" :disabled="disabled"
           :style="{width: `${$attrs.width}px`}" :class="[size,{'is-btn':!isBtn}]"
           @blur="inputNumberBlur" @input="inputNumberBlur">
    <div class="plus" v-if="isBtn" :class="[size,{disabled:max&&inputNumberValue>=max}]" @click="plusClick">
      <i class="iconfont icon-plus fs16"/>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, inject, onMounted, PropType, ref, SetupContext, watch } from 'vue'
import { ComponentSize, componentSizeArr } from '@/base/types'
import { Emitter } from 'mitt'

interface InputNumberProps {
  modelValue: number,
  size: ComponentSize,
  disabled: boolean,
  step: number,
  max: number | null,
  min: number | null
  right: boolean,
  isBtn: boolean
}

export default defineComponent({
  name: 'FInputNumber',
  props: {
    // v-model
    modelValue: {
      type: Number as PropType<number>,
      default: 1,
      required: true
    },
    // 大小
    size: {
      type: String as PropType<ComponentSize>,
      default: 'default',
      validator (val: string): boolean {
        return componentSizeArr.includes(val)
      }
    },
    // 禁用
    disabled: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 步数
    step: {
      type: Number as PropType<number>,
      default: 1
    },
    // 最大
    max: {
      type: Number as PropType<number | null>,
      default: null
    },
    // 最小
    min: {
      type: Number as PropType<number | null>,
      default: null
    },
    // 控制器在右侧
    right: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    isBtn: {
      type: Boolean as PropType<boolean>,
      default: true
    }
  },
  emits: ['update:modelValue', 'change'],
  setup (props: InputNumberProps, ctx: SetupContext) {
    // 当前数值
    const inputNumberValue = ref<number>(props.modelValue)
    // 记录有用的数据 当数据异常是返回正常数据
    let oldRightNumber = 0
    // 接收formItem mitt 监听器
    const formItemMitt = inject<Emitter['emit'] | undefined>('formItemMitt', undefined)

    const handleMaxMin = (): void => {
      if (props.min !== null && inputNumberValue.value <= props.min) {
        inputNumberValue.value = props.min
      }
      if (props.max !== null && inputNumberValue.value >= props.max) {
        inputNumberValue.value = props.max
      }
    }

    // 减少
    const lessClick = (): void => {
      if (props.disabled) return
      inputNumberValue.value -= props.step
      handleMaxMin()
    }
    // 增加
    const plusClick = (): void => {
      if (props.disabled) return
      inputNumberValue.value += props.step
      handleMaxMin()
    }
    // 失焦
    const inputNumberBlur = (): void => {
      if (Number.isNaN(Number(inputNumberValue.value))) {
        inputNumberValue.value = oldRightNumber
      }
      handleMaxMin()
    }

    watch(() => props.modelValue, (val: number): void => {
      inputNumberValue.value = val
    })
    watch(inputNumberValue, (val: number): void => {
      if (!Number.isNaN(Number(val))) oldRightNumber = val
      // 发送当前的数据给formItem
      if (formItemMitt) formItemMitt('formItemValue', inputNumberValue.value)
      ctx.emit('update:modelValue', Number(val))
      ctx.emit('change', Number(val))
    })

    onMounted(() => {
      // 发送当前的数据给formItem
      if (formItemMitt) formItemMitt('formItemValue', inputNumberValue.value)
    })

    return {
      inputNumberValue,
      lessClick,
      plusClick,
      inputNumberBlur
    }
  }
})
</script>

<style scoped lang="scss">
@import "../scss/base";

.fx-input-number {
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;

  &:hover input {
    border-color: $border-color-important;
  }

  &.disabled {
    > input, > .less, > .plus {
      background-color: #f5f7fa;
      cursor: not-allowed;
      border-color: #e4e7ed;
      color: $font-color-lowest !important;
      user-select: none;
    }
  }

  &.right {
    input {
      &.large {
        padding: 0 calc(#{$height-large} + 10px) 0 10px;
      }

      &.middle {
        padding: 0 calc(#{$height-middle} + 10px) 0 10px;;
      }

      &.default {
        padding: 0 calc(#{$height-default} + 10px) 0 10px;;
      }

      &.mini {
        padding: 0 calc(#{$height-mini} + 10px) 0 10px;;
      }
    }

    > .less, > .plus {
      top: 1px;
      right: 1px !important;
      left: auto;
      box-sizing: border-box;

      &.large {
        width: calc(#{$height-large} - 2px);
        height: calc((#{$height-large} - 2px) / 2);
        line-height: calc((#{$height-large} - 2px) / 2);
      }

      &.middle {
        width: calc(#{$height-middle} - 2px);
        height: calc((#{$height-middle} - 2px) / 2);
        line-height: calc((#{$height-middle} - 2px) / 2);
      }

      &.default {
        width: calc(#{$height-default} - 2px);
        height: calc((#{$height-default} - 2px) / 2);
        line-height: calc((#{$height-default} - 2px) / 2);
      }

      &.mini {
        width: calc(#{$height-mini} - 2px);
        height: calc((#{$height-mini} - 2px) / 2);
        line-height: calc((#{$height-mini} - 2px) / 2);
      }
    }

    > .less {
      top: 50%;
      border-top: 1px solid $border-color-default;
    }
  }

  > input {
    width: $input-width-default;
    box-sizing: border-box;
    text-align: center;
    border: 1px solid $border-color-default;
    border-radius: $border-radius-default;

    &.is-btn {
      padding: 0 10px !important;
    }

    &:focus {
      border-color: $color-primary;
    }

    &.large {
      height: $height-large;
      line-height: $height-large;
      padding: 0 calc(#{$height-large} + 10px);
    }

    &.middle {
      height: $height-middle;
      line-height: $height-middle;
      padding: 0 calc(#{$height-middle} + 10px);
    }

    &.default {
      height: $height-default;
      line-height: $height-default;
      padding: 0 calc(#{$height-default} + 10px);
    }

    &.mini {
      width: 130px;
      height: $height-mini;
      line-height: $height-mini;
      padding: 0 calc(#{$height-mini} + 10px);
    }
  }

  > .less, > .plus {
    text-align: center;
    position: absolute;
    top: 1px;
    background-color: #f5f7fa;
    color: #606266;
    cursor: pointer;
    user-select: none;

    &:hover {
      color: $color-primary;
    }

    &.disabled {
      background-color: #f5f7fa;
      cursor: not-allowed;
      border-color: #e4e7ed;
      color: $font-color-lowest !important;
      user-select: none;
    }

    &.large {
      width: calc(#{$height-large} - 2px);
      height: calc(#{$height-large} - 2px);
      line-height: calc(#{$height-large} - 2px);
    }

    &.middle {
      width: calc(#{$height-middle} - 2px);
      height: calc(#{$height-middle} - 2px);
      line-height: calc(#{$height-middle} - 2px);
    }

    &.default {
      width: calc(#{$height-default} - 2px);
      height: calc(#{$height-default} - 2px);
      line-height: calc(#{$height-default} - 2px);
    }

    &.mini {
      width: calc(#{$height-mini} - 2px);
      height: calc(#{$height-mini} - 2px);
      line-height: calc(#{$height-mini} - 2px);
    }
  }

  > .less {
    left: 1px;
    border-radius: $border-radius-default 0 0 $border-radius-default;
  }

  > .plus {
    right: 1px;
    border-radius: 0 $border-radius-default $border-radius-default 0;
  }
}
</style>
