<template>
  <div class="menu-item" :class="{active:index===defaultActive}" @click="menuItemClick">
    <div class="title">
      <slot name="title">{{ title }}</slot>
    </div>
    <div class="content">
      <slot name="default"></slot>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, inject, PropType } from 'vue'
import { MenuMitt } from '@/base/menu/menuTypes'

interface MenuItemProps {
  title: string,
  index: string
}

export default defineComponent({
  name: 'menuItem',
  props: {
    title: {
      type: String as PropType<string>,
      default: ''
    },
    index: {
      type: String as PropType<string>,
      required: true
    }
  },
  setup (props: MenuItemProps) {
    const defaultActive = inject<string>('defaultActive')
    const menuMittEmit = inject<MenuMitt>('menuMitt')

    const menuItemClick = (): void => {
      if (!menuMittEmit?.menuMittEmit) return
      menuMittEmit.menuMittEmit('menuItemClick', props.index)
    }

    return {
      defaultActive,
      menuItemClick
    }
  }
})
</script>

<style scoped lang="scss">
@import "../scss/base";

.menu-item {
  box-sizing: border-box;
  width: 100%;
  height: $height-default;
  line-height: $height-default;
  transition: all 0.3s;
  font-size: 14px;
  color: $font-color-important;
  list-style: none;
  cursor: pointer;
  padding: 0 20px;

  &:hover {
    background-color: $color-primary-1;
  }

  &.active {
    background-color: $color-primary-1;
    color: $color-primary;
  }

  > .content {
    padding-left: 10px;
  }
}
</style>
