<template>
  <div class="fx-select" :class="{active:isFocus,disabled,clear}"
       :style="{width:`${$attrs.width}px`}"
       ref="selectWrapperDom" @click="selectClick">
    <input type="text" :class="[size,{multiple}]"
           v-model="selectLabel" v-bind="$attrs"
           :readonly="selectInputReadonly" :disabled="disabled"
           @focus="selectFocus" @blur="selectBlur">
    <div class="select-icon" :class="{active:modalShow}">
      <i class="select-dropdown iconfont icon-chevron_down fs10"/>
      <i class="select-clear iconfont icon-multiply_circle fs12" @click.stop="clearClick"/>
    </div>
    <div class="select-multiple" :class="[size]" v-if="multiple">
      <span v-for="item in selectLabel" :key="item.value" class="text_hide" :class="{collapse}">
        {{ item.label }}
        <i class="iconfont icon-multiply_circle fs12 ml5"
           @click.stop="multipleClearClick(item.value)"/>
      </span>
      <em v-if="collapse&&selectValue.length>1">+ {{ selectValue.length }}</em>
    </div>
    <div class="select-content" ref="selectContentDom">
      <ul :class="{top:isTopDropdown}" v-show="modalShow">
        <slot></slot>
      </ul>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType, ref, SetupContext, provide, computed, watch, onBeforeMount, onUnmounted } from 'vue'
import { ComponentSize, componentSizeArr } from '@/base/types'
import mitt, { Emitter } from 'mitt'
import { useClickBlank, useModelWatch } from '@/base/hooks'

export type ModelValueProps = string | number | Array<string | number>
export type SelectData = { value: string | number, label: string | number }

interface SelectProps {
  modelValue: ModelValueProps,
  size: ComponentSize,
  multiple: boolean,
  collapse: boolean
  disabled: boolean,
  clear: boolean,
}

export default defineComponent({
  name: 'FSelect',
  props: {
    modelValue: {
      type: [String, Number, Array] as PropType<ModelValueProps>,
      default: '',
      required: true
    },
    // 大小
    size: {
      type: String as PropType<ComponentSize>,
      default: 'default',
      validator (val: string): boolean {
        return componentSizeArr.includes(val)
      }
    },
    // 多选
    multiple: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 多选时 显示一个tag
    collapse: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 禁用
    disabled: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 是否清空
    clear: {
      type: Boolean as PropType<boolean>,
      default: false
    }
  },
  emits: ['update:modelValue', 'focus', 'blur', 'change'],
  setup (props: SelectProps, ctx: SetupContext) {
    // 是否显示下拉框
    const modalShow = ref<boolean>(false)
    // select选中框状态
    const selectInputReadonly = ref<boolean>(true)
    // 当前选中
    const selectValue = ref<ModelValueProps>(props.modelValue)
    // 当前选中显示文字
    const selectLabel = computed(() => {
      if (selectValue.value instanceof Array) {
        const arr: SelectData[] = []
        selectValue.value.forEach((item: string | number) => {
          const has = selectData.value.find((child: SelectData) => child.value === item)
          if (has) arr.push(has)
        })
        return props.collapse ? arr.length ? [arr[0]] : [] : arr
      } else {
        const index = selectData.value.findIndex((item: SelectData) => item.value === selectValue.value)
        return index < 0 ? '' : selectData.value[index].label
      }
    })
    // 所有下拉选择数据
    const selectData = ref<SelectData[]>([])
    // 是否聚焦
    const isFocus = ref<boolean>(false)
    // selectInput Dom
    const selectWrapperDom = ref<HTMLElement | undefined>(undefined)
    // selectContent Dom
    const selectContentDom = ref<HTMLElement | undefined>(undefined)
    // 定位向上展示弹窗下拉
    const isTopDropdown = ref<boolean>(false)
    // 数据监听
    const selectMitt: Emitter = mitt()

    provide('selectValue', computed(() => selectValue.value))
    provide('selectMittEmit', selectMitt.emit)

    // select组件聚焦
    const selectFocus = (ev: Event): void => {
      isFocus.value = true
      ctx.emit('focus', ev)
    }
    // select组件失焦
    const selectBlur = (ev: Event): void => {
      isFocus.value = false
      ctx.emit('blur', ev)
    }
    // 处理下拉框定位
    const handlePosition = (): void => {
      if (!selectWrapperDom.value || !selectContentDom.value) return
      const rect = selectWrapperDom.value.getBoundingClientRect()
      const { height: contentHeight } = selectContentDom.value.getBoundingClientRect()
      const { clientHeight } = document.body
      selectContentDom.value.style.width = `${rect.width}px`
      selectContentDom.value.style.left = `${rect.left}px`
      selectContentDom.value.style.top = `${rect.top + rect.height}px`
      if ((rect.top + rect.height + contentHeight + 20) >= clientHeight) {
        isTopDropdown.value = true
        selectContentDom.value.style.top = `${rect.top - contentHeight - 20}px`
      } else {
        isTopDropdown.value = false
      }
    }
    // 监听option点击
    const handleSelectMitt = (val: SelectData | undefined): void => {
      if (!val) return
      if (props.multiple && selectValue.value instanceof Array) {
        const index = selectValue.value.findIndex((item: string | number) => val.value === item)
        index < 0 ? selectValue.value.push(val.value) : selectValue.value.splice(index, 1)
      } else {
        selectValue.value = val.value
      }
    }
    // 监听option数据返回 整理所有数据
    const handleSelectDataMitt = (val: SelectData | undefined): void => {
      if (!val) return
      selectData.value.push(val)
    }
    // 选项点击
    const selectClick = (): void => {
      if (props.disabled) return
      modalShow.value = !modalShow.value
      setTimeout(handlePosition)
    }
    // 清空
    const clearClick = (): void => {
      selectValue.value = props.multiple ? [] : ''
    }
    // 多选框 选择删除
    const multipleClearClick = (val: string | number): void => {
      if (props.multiple && selectValue.value instanceof Array) {
        const index = selectValue.value.findIndex((item: string | number) => item === val)
        selectValue.value.splice(index, 1)
      }
    }
    // 数据双向绑定hook
    useModelWatch<ModelValueProps>(() => props.modelValue, selectValue)
    // 处理点击空白隐藏hook
    useClickBlank(selectWrapperDom, modalShow)

    watch(modalShow, (val: boolean): void => {
      if (!val) {
        document.removeEventListener('mousedown', handlePosition, true)
      } else {
        document.addEventListener('scroll', handlePosition, true)
      }
    })

    onBeforeMount(() => {
      selectMitt.on<SelectData | undefined>('selectMitt', handleSelectMitt)
      selectMitt.on<SelectData | undefined>('selectDataMitt', handleSelectDataMitt)
      document.addEventListener('scroll', handlePosition, true)
    })

    onUnmounted(() => {
      document.removeEventListener('scroll', handlePosition, true)
    })

    return {
      modalShow,
      selectInputReadonly,
      selectWrapperDom,
      selectContentDom,
      selectValue,
      selectLabel,
      isFocus,
      isTopDropdown,
      selectFocus,
      selectBlur,
      handlePosition,
      selectClick,
      clearClick,
      multipleClearClick
    }
  }
})
</script>

<style scoped lang="scss">
@import "../scss/base";

.fx-select {
  box-sizing: border-box;
  width: $input-width-default;
  display: inline-block;
  border: 1px solid $border-color-default;
  border-radius: $border-radius-default;
  position: relative;
  color: $font-color-default;
  cursor: pointer;
  user-select: none;

  &.clear:hover {
    border-color: $border-color-important;

    > .select-icon {
      > .select-dropdown {
        display: none;
      }

      > .select-clear {
        display: inline-block;
      }
    }
  }

  &.active {
    border-color: $color-primary;
  }

  &.disabled {
    > input {
      background-color: #f5f7fa;
      cursor: not-allowed;
      color: $font-color-lowest;
    }

    > .select-icon {
      color: $font-color-lowest;
    }
  }

  > input {
    width: 100%;
    border-radius: $border-radius-default;
    display: inline-block;
    box-sizing: border-box;
    padding: 0 30px 0 10px;
    cursor: pointer;

    &.multiple {
      position: absolute;
      z-index: -1;
      color: transparent !important;
      height: 100% !important;
    }

    &.large {
      height: $height-large;
      line-height: $height-large;
    }

    &.middle {
      height: $height-middle;
      line-height: $height-middle;
    }

    &.default {
      height: $height-default;
      line-height: $height-default;
    }

    &.mini {
      height: $height-mini;
      line-height: $height-mini;
    }
  }

  > .select-icon {
    display: flex;
    color: $font-color-default;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: 0;
    top: 0;
    width: 25px;
    height: 100%;
    text-align: center;

    &.active > .select-dropdown {
      transform-origin: center center;
      transform: rotate(180deg);
    }

    > .select-clear {
      display: none;
    }

    > .select-dropdown {
      transition: all 0.15s;
    }
  }

  > .select-content {
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    padding: 10px 0 5px 0;
    overflow: hidden;

    @keyframes fx-select-dropdown-animation-bottom {
      from {
        transform: translateY(-100%);
      }
      to {
        transform: translateY(0);
      }
    }

    @keyframes fx-select-dropdown-animation-top {
      from {
        transform: translateY(100%);
      }
      to {
        transform: translateY(0);
      }
    }

    > ul {
      width: 100%;
      height: 100%;
      max-height: 200px;
      border-radius: $border-radius-default;
      overflow-y: auto;
      background-color: #fff;
      border: 1px solid $border-color-default;
      box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .1);
      box-sizing: border-box;
      transform-origin: top center;
      transition: all 0.3s;
      animation: fx-select-dropdown-animation-bottom 0.15s;

      &::after {
        content: '';
        position: absolute;
        top: -1px;
        left: 35px;
        display: block;
        width: 0;
        height: 0;
        border-color: transparent;
        border-style: solid;
        margin-right: 3px;
        border-bottom-color: #fff;
        border-width: 6px;
        filter: drop-shadow(0 2px 12px rgba(0, 0, 0, .03));
      }

      &.top {
        animation: fx-select-dropdown-animation-top 0.15s;
      }
    }
  }

  > .select-multiple {
    padding-right: 30px;
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;

    &.large {
      min-height: $height-large;
    }

    &.middle {
      min-height: $height-middle;
    }

    &.default {
      min-height: $height-default;
    }

    &.mini {
      min-height: $height-mini;
    }

    > span, em {
      display: inline-block;
      max-width: calc(100% - 30px);
      background-color: #f4f4f5;
      border: 1px solid #e9e9eb;
      border-radius: 4px;
      color: #909399;
      height: 24px;
      padding: 0 20px 0 8px;
      margin: 4px 0 2px 6px;
      line-height: 22px;
      position: relative;

      &.collapse {
        max-width: calc(100% - 80px);
      }

      > i {
        position: absolute;
        right: 5px;
        top: 50%;
        transform: translateY(-50%);

        &:hover {
          color: $color-error;
        }
      }
    }

    em {
      padding-right: 8px;
    }
  }
}
</style>
