/** 颜色转化为 rgba 格式
 * @param {string} colorText 颜色 #xxx rgb(x,x,x) 格式
 * @param {number} alpha 透明度 0-1 自然数
 */
export const colorRgb = (colorText: string, alpha = 1): string => {
  let color = colorText.toLowerCase()
  if (/^(rgba|RGBA)/.test(colorText)) {
    const a = colorText.split(',')
    let b = ''
    a.forEach((item: string, index: number) => {
      if (index < a.length - 1) b += `${item},`
    })
    return `${b} ${alpha})`
  }
  if (/^(rgb|RGB)/.test(colorText)) {
    color = color.replace(/^(rgb|RGB)/, 'rgba')
    color = color.replace(/\)$/, `, ${alpha})`)
    return color
  }
  const reg = /^#([0-9a-fA-f]{3}|[0-9a-fA-f]{6})$/
  if (reg.test(color)) {
    if (color.length === 4) {
      let colorNew = '#'
      for (let i = 1; i < 4; i += 1) {
        colorNew += color.slice(i, i + 1).concat(color.slice(i, i + 1))
      }
      color = colorNew
    }
    const colorChange = []
    for (let i = 1; i < 7; i += 2) {
      colorChange.push(parseInt('0x' + color.slice(i, i + 2)))
    }
    return `rgba(${colorChange.join(',')},${alpha})`
  } else {
    return color
  }
}

/** 随机字符
 * @param num 随机字符个数
 */
const randomWord = (num: number): string => {
  let randomText = ''
  const arr = 'abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ'.split('')
  Array.from(new Array(num)).forEach((): void => {
    randomText += arr[Math.floor((arr.length - 1) * Math.random())]
  })
  return randomText
}
// 随机ID
export const randomId = (num = 10): string => {
  return `fx-${randomWord(num)}-${Math.floor(new Date().getTime() * Math.random())}`
}
/** 给dom添加class
 * @param dom
 * @param className
 */
export const addClassName = (dom: HTMLElement, className: string): void => {
  const isClassName = new RegExp(className, 'g').test(dom.className)
  if (!isClassName) dom.className += ` ${className}`
}
/** 移除dom上的class
 * @param dom
 * @param className
 */
export const removeClassName = (dom: HTMLElement, className: string): void => {
  dom.className = dom.className.replace(new RegExp(className, 'g'), '').trim()
}

/** 骇客技术添加css样式
 * @param style style名
 */
export const prefixStyle = (style: string): string => {
  const vendor = (() => {
    const transformName = {
      webkit: 'webkitTransform',
      Moz: 'MozTransform',
      O: 'OTransform',
      ms: 'msTransform',
      standard: 'transform'
    }
    for (const key in transformName) {
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      const domKey = document.createElement('div').style[transformName[key]]
      if (domKey !== undefined) return key
    }
    return false
  })()
  if (!vendor) return ''
  if (vendor === 'standard') return style
  return vendor + style.charAt(0).toUpperCase() + style.substr(1)
}
