<template>
  <div class="fx-radio" @click="radioClick"
       :class="[{active:(modelValue===label||radioGroupValue===label),
       disabled:(radioGroupDisabled||disabled),border},radioGroupSize!==''?radioGroupSize:size]">
    <div class="fx-radio-select-box">
      <input type="radio" hidden :name="currentRadioValue" :value="label">
    </div>
    <div class="fx-radio-label">
      <slot></slot>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, inject, onMounted, PropType, ref, SetupContext } from 'vue'
import { ComponentSize, componentSizeArr } from '@/base/types'
import { Emitter } from 'mitt'
import { useModelWatch } from '@/base/hooks'

interface RadioProps {
  modelValue: string | number,
  label: string | number,
  disabled: boolean,
  size: ComponentSize,
  border: boolean
}

export default defineComponent({
  name: 'FRadio',
  props: {
    // v-model
    modelValue: {
      type: [String, Number] as PropType<string | number>,
      default: ''
    },
    // 选项
    label: {
      type: [String, Number] as PropType<string | number>,
      default: '',
      required: true
    },
    // 禁用
    disabled: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 大小
    size: {
      type: String as PropType<ComponentSize>,
      default: 'default',
      validator (val: string): boolean {
        return componentSizeArr.includes(val)
      }
    },
    // 边框
    border: {
      type: Boolean as PropType<boolean>,
      default: false
    }
  },
  emits: ['update:modelValue', 'change'],
  setup (props: RadioProps, ctx: SetupContext) {
    // 获取来自radioGroup emit
    const radioGroupEmit = inject<Emitter['emit'] | undefined>('radioGroupMittEmit', undefined)
    // 获取来自radioGroup 选中
    const radioGroupValue = inject('radioGroupValue', { value: '' })
    // 获取来自radioGroup 禁用
    const radioGroupDisabled = inject('radioGroupDisabled', false)
    // 获取来自radioGroup 大小
    const radioGroupSize = inject('radioGroupSize', '')
    // 当前选中
    const currentRadioValue = ref<string | number>(props.modelValue)
    // 数据双向绑定hook
    useModelWatch<string | number>(() => props.modelValue, currentRadioValue)
    // 接收formItem mitt 监听器
    const formItemMitt = inject<Emitter['emit'] | undefined>('formItemMitt', undefined)

    // 单选点击
    const radioClick = (): void => {
      if (props.disabled || radioGroupDisabled) return
      // 当存在radioGroup父级时生效
      if (radioGroupEmit) radioGroupEmit('radioGroupClick', props.label)
      currentRadioValue.value = props.label
      ctx.emit('change', props.label)
      // 发送当前的数据给formItem
      if (formItemMitt) formItemMitt('formItemValue', currentRadioValue.value)
    }

    onMounted(() => {
      // 发送当前的数据给formItem
      if (formItemMitt) formItemMitt('formItemValue', currentRadioValue.value)
    })

    return {
      radioGroupSize,
      radioGroupValue,
      currentRadioValue,
      radioGroupDisabled,
      radioClick
    }
  }
})
</script>

<style scoped lang="scss">
@import "../scss/base";

.fx-radio {
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
  margin-right: 10px;
  user-select: none;
  cursor: pointer;
  transition: transform .15s ease-in;

  &:hover > .fx-radio-select-box {
    border-color: $color-primary;
  }

  &.active {
    color: $color-primary;

    &.disabled {
      > .fx-radio-select-box {
        background-color: $font-color-lowest !important;
        border-color: $font-color-lowest !important;
      }
    }

    > .fx-radio-select-box {
      background-color: $color-primary;
      border-color: $color-primary;

      &::after {
        transform: translate(-50%, -50%) scale(1);
      }
    }
  }

  &.disabled {
    cursor: not-allowed !important;
    color: $font-color-lowest !important;

    &:hover {
      > .fx-radio-select-box {
        border-color: $font-color-lowest;
      }
    }
  }

  &.border {
    padding: 0 20px 0 10px;
    border: 1px solid $border-color-default;
    background-color: #fff;
    border-radius: $border-radius-default;

    &.active {
      border-color: $color-primary;
    }

    &.disabled {
      border-color: $font-color-lowest !important;
    }

    &.large {
      height: $height-large;
      line-height: $height-large;
    }

    &.middle {
      height: $height-middle;
      line-height: $height-middle;
    }

    &.default {
      height: $height-default;
      line-height: $height-default;
    }

    &.mini {
      height: $height-mini;
      line-height: $height-mini;
    }
  }

  > .fx-radio-select-box {
    display: inline-block;
    border: 1px solid $font-color-lowest;
    border-radius: 50%;
    width: 14px;
    height: 14px;
    background-color: #fff;
    position: relative;
    box-sizing: border-box;

    &::after {
      content: "";
      display: inline-block;
      width: 4px;
      height: 4px;
      border-radius: 100%;
      background-color: #fff;
      position: absolute;
      left: 50%;
      top: 50%;
      z-index: 10;
      transform: translate(-50%, -50%) scale(0);
      transition: transform .15s ease-in;
    }
  }

  > .fx-radio-label {
    padding-left: 5px;
  }
}
</style>
