<template>
  <li class="fx-option" v-bind="$attrs"
      :class="{active:activeClass,disabled}" @click="optionClick">
    <slot>{{ label }}</slot>
  </li>
</template>

<script lang="ts">
import { defineComponent, PropType, inject, onMounted, Ref, computed } from 'vue'
import { ModelValueProps } from '../select/select.vue'
import { Emitter } from 'mitt'

interface SelectOptionProps {
  label: string | number,
  value: string | number,
  disabled: boolean
}

export default defineComponent({
  name: 'FOption',
  inheritAttrs: true,
  props: {
    label: {
      type: [String, Number] as PropType<string | number>,
      default: '',
      required: true
    },
    value: {
      type: [String, Number] as PropType<string | number>,
      default: '',
      required: true
    },
    // 禁用
    disabled: {
      type: Boolean as PropType<boolean>,
      default: false
    }
  },
  setup (props: SelectOptionProps) {
    // 当前选中
    const currentSelectValue = inject<Ref<ModelValueProps> | undefined>('selectValue', undefined)
    // 事件监听
    const selectMittEmit = inject<Emitter['emit']>('selectMittEmit')

    // 点击
    const optionClick = (): void => {
      if (props.disabled) return
      if (selectMittEmit) {
        selectMittEmit('selectMitt', {
          label: props.label,
          value: props.value
        })
      }
    }
    // 当前选中
    const activeClass = computed<boolean>(() => {
      const selectValue = currentSelectValue
      if (!selectValue) return false
      if (selectValue.value instanceof Array) {
        return selectValue.value.some((item: string | number) => item && item === props.value)
      } else {
        return selectValue.value === props.value
      }
    })

    onMounted(() => {
      if (selectMittEmit) {
        selectMittEmit('selectDataMitt', {
          label: props.label,
          value: props.value
        })
      }
    })

    return {
      currentSelectValue,
      activeClass,
      optionClick
    }
  }
})
</script>

<style scoped lang="scss">
@import "../scss/base";

.fx-option {
  font-size: 14px;
  padding: 0 20px;
  position: relative;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #606266;
  height: 34px;
  line-height: 34px;
  box-sizing: border-box;
  cursor: pointer;

  &:hover {
    background-color: $border-color-lowest;
  }

  &.active {
    background-color: $border-color-lowest;
    color: $color-primary;
  }

  &.disabled {
    background-color: #fff;
    cursor: not-allowed;
    color: $font-color-lowest;
  }
}
</style>
