<template>
  <div class="avatar" :class="[typeof size==='string'?size:null,shape]" :style="avatarStyle">
    <slot v-if="$slots.default||failStatus"></slot>
    <img v-else-if="src&&src!==''&&!failStatus" :src="src"
         :alt="$attrs.alt" :style="{objectFit:fit}" @error="failStatus=true">
    <i v-else-if="icon&&icon!==''&&!failStatus" class="fs18 iconfont" :class="icon"/>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, PropType, ref } from 'vue'
import { ComponentSize, componentSizeArr } from '@/base/types'

interface AvatarProps {
  src: string,
  size: ComponentSize | number,
  shape: 'circle' | 'square',
  fit: 'fill' | 'contain' | 'cover' | 'none' | 'scale-down',
  icon: string
}

export default defineComponent({
  name: 'FAvatar',
  props: {
    // 图片地址
    src: {
      type: String as PropType<string>,
      default: null
    },
    // 大小
    size: {
      type: [String, Number] as PropType<ComponentSize | number>,
      default: 'default',
      validator (val: string | number): boolean {
        if (typeof val === 'string') return componentSizeArr.includes(val)
        return Number.isInteger(val)
      }
    },
    // 头像样式
    shape: {
      type: String as PropType<'circle' | 'square'>,
      default: 'circle',
      validator (val: string): boolean {
        return ['circle', 'square'].includes(val)
      }
    },
    // 头像样式
    fit: {
      type: String as PropType<'fill' | 'contain' | 'cover' | 'none' | 'scale-down'>,
      default: 'contain',
      validator (val: string): boolean {
        return ['fill', 'contain', 'cover', 'none', 'scale-down'].includes(val)
      }
    },
    // icon
    icon: {
      type: String as PropType<string>,
      default: null
    }
  },
  setup (props: AvatarProps) {
    // 头像样式
    const avatarStyle = computed(() => {
      if (typeof props.size === 'string') return null
      return {
        width: `${props.size}px`,
        height: `${props.size}px`,
        lineHeight: `${props.size}px`
      }
    })
    // 加载错误状态
    const failStatus = ref<boolean>(false)

    return {
      avatarStyle,
      failStatus
    }
  }
})
</script>

<style scoped lang="scss">
@import "../scss/base";

.avatar {
  position: relative;
  display: inline-block;
  background-color: #c0c4cc;
  color: #fff;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;

  &.large {
    width: $height-large;
    height: $height-large;
    line-height: $height-large;
  }

  &.middle {
    width: $height-middle;
    height: $height-middle;
    line-height: $height-middle;
  }

  &.default {
    width: $height-default;
    height: $height-default;
    line-height: $height-default;
  }

  &.mini {
    width: $height-mini;
    height: $height-mini;
    line-height: $height-mini;
  }

  &.square {
    border-radius: $border-radius-default;
  }

  &.circle {
    border-radius: 50%;
  }

  > img {
    width: 100%;
    height: 100%;
    border-radius: $border-radius-default;
  }
}
</style>
