
import { defineComponent, PropType, SetupContext } from 'vue'
import { ComponentSize, componentSizeArr, ComponentTypes, componentTypesArr } from '@/base/types'

export declare interface ButtonProps {
  type: ComponentTypes | 'text',
  size: ComponentSize,
  plain: boolean,
  round: boolean,
  circle: boolean,
  disabled: boolean,
  loading: boolean,
  icon: string
}

export default defineComponent({
  name: 'FButton',
  emits: ['click'],
  inheritAttrs: true,
  props: {
    // 样式类型
    type: {
      type: String as PropType<ComponentTypes | 'text'>,
      default: '',
      validator (value: string): boolean {
        return [...componentTypesArr, 'text'].includes(value)
      }
    },
    // 大小类型
    size: {
      type: String as PropType<ComponentSize>,
      default: 'default',
      validator (value: string): boolean {
        return componentSizeArr.includes(value)
      }
    },
    // 镂空
    plain: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 圆角
    round: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 圆角
    circle: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 禁用
    disabled: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 加载
    loading: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 图标
    icon: {
      type: String as PropType<string>,
      default: ''
    }
  },
  setup (props: ButtonProps, ctx: SetupContext) {
    // 点击事件
    const handleButtonClick = (ev: MouseEvent): void => {
      if (props.disabled || props.loading) return
      ctx.emit('click', ev)
    }

    return {
      handleButtonClick
    }
  }
})
