import HighLight from 'highlight.js'
import 'highlight.js/styles/color-brewer.css'
import { App } from 'vue'

const initHighLight = (app: App): void => {
  app.directive('highlight', {
    beforeMount (el, binding) {
      const code = el.querySelectorAll('pre code')
      code.forEach((item: HTMLElement) => {
        if (typeof binding.value === 'string') {
          item.textContent = binding.value
        }
        HighLight.highlightBlock(item)
      })
    },
    updated (el, binding) {
      const code = el.querySelectorAll('code')
      code.forEach((item: HTMLElement) => {
        if (typeof binding.value === 'string') {
          item.textContent = binding.value
        }
        HighLight.highlightBlock(item)
      })
    }
  })
}

export default initHighLight
