<template>
  <div class="tooltip" :class="[position,theme]" :style="{maxWidth:`${width}px`}">
    <slot><span style="white-space: pre-line;">{{ titleValue }}</span></slot>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType, ref } from 'vue'
import { ComponentTheme } from '@/base/types'

export type TooltipPosition =
  'topLeft'
  | 'top'
  | 'topRight'
  | 'rightTop'
  | 'right'
  | 'rightBottom'
  | 'bottomLeft'
  | 'bottom'
  | 'bottomRight'
  | 'leftTop'
  | 'left'
  | 'leftBottom'

export interface TooltipProps {
  title: string | number;
  position: TooltipPosition;
  width: string | number;
  theme: ComponentTheme
}

export const TooltipPositionArr = ['topLeft', 'top', 'topRight', 'rightTop', 'right', 'rightBottom', 'bottomLeft', 'bottom', 'bottomRight', 'leftTop', 'left', 'leftBottom']

export default defineComponent({
  name: 'tooltip',
  props: {
    // 提示文字
    title: {
      type: [String, Number] as PropType<string | number>,
      default: '',
      required: true
    },
    // 提示方向
    position: {
      type: String as PropType<TooltipPosition>,
      default: 'top',
      validator (val: string): boolean {
        return TooltipPositionArr.includes(val)
      }
    },
    width: {
      type: [String, Number] as PropType<string | number>,
      default: 220
    },
    theme: {
      type: String as PropType<ComponentTheme>,
      default: 'dark'
    }
  },
  setup (props: TooltipProps) {
    const titleValue = ref<string | number>(props.title)

    const setTitleValue = (val: string | number): void => {
      titleValue.value = val
    }

    return {
      titleValue,
      setTitleValue
    }
  }
})
</script>

<style scoped lang="scss">
@import "../../scss/base";

.tooltip {
  position: fixed;
  z-index: 1000;
  height: fit-content;
  padding: 5px 15px;
  background-color: #303133;
  color: #fff;
  border-radius: $border-radius-default;
  border: 1px solid #303133;

  &::after {
    content: '';
    position: absolute;
    border: 6px solid transparent;
  }

  &.plain {
    background-color: #fff;
    color: $color-primary;
    border-color: $color-primary;

    &::before {
      content: '';
      position: absolute;
      z-index: 10;
      border: 5px solid transparent;
    }
  }

  &.light {
    background-color: #fff;
    color: $font-color-default;
    border-color: #303133;

    &::before {
      content: '';
      position: absolute;
      z-index: 10;
      border: 5px solid transparent;
    }
  }

  &.topLeft, &.top, &.topRight {
    &.plain {
      border-top-color: $color-primary;

      &::after {
        border-top-color: $color-primary;
      }

      &::before {
        border-top-color: #fff;
      }
    }
  }

  &.topLeft {
    transform: translateY(-100%);

    &::after {
      left: 12px;
      bottom: -12px;
      border-top-color: #303133;
    }

    &::before {
      left: 13px;
      bottom: -10px;
      border-top-color: #fff;
    }
  }

  &.top {
    transform: translateX(-50%) translateY(-100%);

    &::after {
      left: 50%;
      bottom: -12px;
      transform: translateX(-50%);
      border-top-color: #303133;
    }

    &::before {
      left: 50%;
      bottom: -10px;
      transform: translateX(-50%);
      border-top-color: #fff;
    }
  }

  &.topRight {
    transform: translateX(-100%) translateY(-100%);

    &::after {
      right: 12px;
      bottom: -12px;
      border-top-color: #303133;
    }

    &::before {
      right: 13px;
      bottom: -10px;
      border-top-color: #fff;
    }
  }

  &.rightTop, &.right, &.rightBottom {
    &.plain {
      border-right-color: $color-primary;

      &::after {
        border-right-color: $color-primary;
      }

      &::before {
        border-right-color: #fff;
      }
    }
  }

  &.rightTop {
    &::after {
      left: -12px;
      top: 12px;
      border-right-color: #303133;
    }

    &::before {
      left: -10px;
      top: 13px;
      border-right-color: #fff;
    }
  }

  &.right {
    transform: translateY(-50%);

    &::after {
      left: -12px;
      top: 50%;
      transform: translateY(-50%);
      border-right-color: #303133;
    }

    &::before {
      left: -10px;
      top: 50%;
      transform: translateY(-50%);
      border-right-color: #fff;
    }
  }

  &.rightBottom {
    transform: translateY(-100%);

    &::after {
      left: -12px;
      bottom: 12px;
      border-right-color: #303133;
    }

    &::before {
      left: -10px;
      bottom: 13px;
      border-right-color: #fff;
    }
  }

  &.leftTop, &.left, &.leftBottom {
    &.plain {
      border-left-color: $color-primary;

      &::after {
        border-left-color: $color-primary;
      }

      &::before {
        border-left-color: #fff;
      }
    }
  }

  &.leftTop {
    transform: translateX(-100%);

    &::after {
      right: -12px;
      top: 12px;
      border-left-color: #303133;
    }

    &::before {
      right: -9px;
      top: 13px;
      border-left-color: #fff;
    }
  }

  &.left {
    transform: translateX(-100%) translateY(-50%);

    &::after {
      right: -12px;
      top: 50%;
      transform: translateY(-50%);
      border-left-color: #303133;
    }

    &::before {
      right: -9px;
      top: 50%;
      transform: translateY(-50%);
      border-left-color: #fff;
    }
  }

  &.leftBottom {
    transform: translateX(-100%) translateY(-100%);

    &::after {
      right: -12px;
      bottom: 12px;
      border-left-color: #303133;
    }

    &::before {
      right: -9px;
      bottom: 13px;
      border-left-color: #fff;
    }
  }

  &.bottomLeft, &.bottom, &.bottomRight {
    &.plain {
      border-bottom-color: $color-primary;

      &::after {
        border-bottom-color: $color-primary;
      }

      &::before {
        border-bottom-color: #fff;
      }
    }
  }

  &.bottomLeft {
    &::after {
      left: 12px;
      top: -12px;
      border-bottom-color: #303133;
    }

    &::before {
      left: 13px;
      top: -10px;
      border-bottom-color: #fff;
    }
  }

  &.bottom {
    transform: translateX(-50%);

    &::after {
      left: 50%;
      top: -12px;
      transform: translateX(-50%);
      border-bottom-color: #303133;
    }

    &::before {
      left: 50%;
      top: -10px;
      transform: translateX(-50%);
      border-bottom-color: #fff;
    }
  }

  &.bottomRight {
    transform: translateX(-100%);

    &::after {
      right: 12px;
      top: -12px;
      border-bottom-color: #303133;
    }

    &::before {
      right: 13px;
      top: -10px;
      border-bottom-color: #fff;
    }
  }
}
</style>
