<template>
  <div class="fx-badge" v-bind="$attrs" :class="[type,{dot:isDot}]">
    <span class="mark">
      <em v-if="!isDot">{{ badgeValue }}</em>
    </span>
    <slot></slot>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, PropType } from 'vue'
import { ComponentTypes, componentTypesArr } from '@/base/types'

interface BadgeProps {
  modelValue: number | string;
  type: ComponentTypes;
  max: number;
  isDot: boolean;
}

export default defineComponent({
  name: 'FBadge',
  inheritAttrs: true,
  props: {
    modelValue: {
      type: [String, Number] as PropType<string | number>,
      default: ''
    },
    // 类型
    type: {
      type: String as PropType<ComponentTypes>,
      default: 'error',
      validator (val: string): boolean {
        return componentTypesArr.includes(val)
      }
    },
    // 最大值
    max: {
      type: Number as PropType<number>,
      default: null
    },
    // 是否红点
    isDot: {
      type: Boolean as PropType<boolean>,
      default: false
    }
  },
  setup (props: BadgeProps) {
    const badgeValue = computed<string | number>(() => {
      const isNaN = Number.isNaN(Number(props.modelValue))
      if (!isNaN && props.max && props.modelValue >= props.max) return `${props.max}+`
      return props.modelValue
    })

    return {
      badgeValue
    }
  }
})
</script>

<style scoped lang="scss">
@import "../scss/base";

.fx-badge {
  display: inline-block;
  position: relative;

  &.primary {
    > .mark {
      background-color: $color-primary;
    }
  }

  &.success {
    > .mark {
      background-color: $color-success;
    }
  }

  &.warning {
    > .mark {
      background-color: $color-warning;
    }
  }

  &.error {
    > .mark {
      background-color: $color-error;
    }
  }

  &.dot {
    > .mark {
      min-width: 10px !important;
      width: 10px !important;
      height: 10px !important;
      padding: 0;
      border-radius: 50%;
      border: 1px solid #fff;
      right: 5px;
    }
  }

  > .mark {
    box-sizing: border-box;
    color: #fff;
    display: inline-block;
    padding: 0 6px;
    position: absolute;
    min-width: 20px;
    height: 20px;
    line-height: 20px;
    border-radius: 10px;
    z-index: 10;
    top: 0;
    right: 10px;
    transform: translateY(-50%) translateX(100%);
    white-space: nowrap;
  }
}
</style>
